import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTable, MatTableDataSource } from '@angular/material/table';
import { ApiService, DataStorageService } from '@services';
import { ENDPOINTS } from '@shared';
import { ModalMultipleApplicationsComponent } from './modal-multiple-applications/modal-multiple-applications.component';


export interface companyDetails {
  referenceCode: string;
  amount: number;
}

const ELEMENT_DATA: companyDetails[] = [
  { referenceCode: 'Company one ', amount: 3000, },
  { referenceCode: 'Company two ', amount: 4000, },
  { referenceCode: 'Company three ', amount: 5000, },
  { referenceCode: 'Company two ', amount: 6000, },
  { referenceCode: 'Company three ', amount: 7000, },
];

@Component({
  selector: 'app-multiple-applications',
  templateUrl: './multiple-applications.component.html',
  styleUrls: ['./multiple-applications.component.scss']
})
export class MultipleApplicationsComponent implements OnInit {

  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: false }) sort: MatSort;
  @ViewChild(MatTable, { static: false }) table: MatTable<Object>;

  dataSource: MatTableDataSource<any>
  displayedColumns: string[] = ['adTitle', 'typeOfApplication', 'brand', 'product', 'applicantCompanyName', 'paymentStatus'];
  paymentList = [];
  currentUser: any;


  constructor(
    private apiService: ApiService,
    private dialog: MatDialog,
    private dataStorage: DataStorageService,
  ) { }

  ngOnInit(): void {
    this.currentUser = this.dataStorage.getUserAccount();
    this.getS1MultiplePaymentList();
  }

  getS1MultiplePaymentList() {
    this.apiService.findAll(ENDPOINTS.applicationFormPresentor + `${this.currentUser.id}/multiple-application?paymentStatus=FOR_PAYMENT`)
    .subscribe((res: any) => {
      this.paymentList = res.responseData.data;

      this.dataSource = new MatTableDataSource<any>(this.paymentList);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }, (err: any) => {
    });
  }

  openModal(res) {
    const dialogRef = this.dialog.open(ModalMultipleApplicationsComponent, {
      width: '60%',
      height: '85%',
      data: res
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        console.log(result);
        this.getS1MultiplePaymentList();
      }
    });

  }


}
