import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { URL_PATHS } from '@shared';

const routes: Routes = [
  {
    path: URL_PATHS.S1_APPLICATION,
    loadChildren: () => import('./s1-application/s1-application.module')
      .then(m => m.S1ApplicationModule)
  },
  {
    path: URL_PATHS.S2_APPLICATION,
    loadChildren: () => import('./s2-application/s2-application.module')
      .then(m => m.S2ApplicationModule)
  },
  {
    path: URL_PATHS.SCHEDULE_OF_FEES_SINGLE_APPLICATION,
    loadChildren: () => import('./schedule-of-fees/single-application/single-application.module')
      .then(m => m.SingleApplicationModule)
  },
  {
    path: URL_PATHS.SCHEDULE_OF_FEES_SINGLE_APPLICATION + '/:id',
    loadChildren: () => import('./schedule-of-fees/single-application/single-application.module')
      .then(m => m.SingleApplicationModule)
  },
  {
    path: URL_PATHS.SCHEDULE_OF_FEES_MULTIPLE_APPLICATIONS,
    loadChildren: () => import('./schedule-of-fees/multiple-applications/multiple-applications.module')
      .then(m => m.MultipleApplicationsModule)
  },
  {
    path: URL_PATHS.SCHEDULE_OF_FEES_MULTIPLE_APPLICATIONS + '/:id',
    loadChildren: () => import('./schedule-of-fees/multiple-applications/multiple-applications.module')
      .then(m => m.MultipleApplicationsModule)
  },
  {
    path: URL_PATHS.SCHEDULE_OF_FEES_SPECIAL_APPLICATIONS,
    loadChildren: () => import('./schedule-of-fees/special-applications/special-applications.module')
      .then(m => m.SpecialApplicationsModule)
  },
  {
    path: URL_PATHS.ACCOUNTS_PAYABLE,
    loadChildren: () => import('./schedule-of-fees/accounts-payable/accounts-payable.module')
      .then(m => m.AccountsPayableModule)
  },
  {
    path: URL_PATHS.SCHEDULE_OF_FEES_SPECIAL_APPLICATIONS + '/:id',
    loadChildren: () => import('./schedule-of-fees/special-applications/special-applications.module')
      .then(m => m.SpecialApplicationsModule)
  },
  {
    path: URL_PATHS.APPLICATION_REASSIGNMENT,
    loadChildren: () => import('./reassignment/reassignment.module')
      .then(m => m.ReassignmentModule)
  }
];
@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ApplicationsRoutingModule { }
