import { AngularEditorConfig } from './../editor/lib/config';
import { CommentThread } from './../../../../core/models/comments.model';
import { DatePipe } from '@angular/common';
import { Component, Inject, Input, OnInit, ViewChild, ElementRef } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { COMMENT_TYPE, APPFORM_TYPE, APPLICATION_STAGE, APPLICATION_STATUS_FOR_COMPLIANCE, APPLICATION_STATUS } from '@shared';
import { S1Application, S2Application } from '@models';
import { ApiService, DataStorageService, SweetAlertService } from '@services';
import { ENDPOINTS } from '@shared';
import Swal from 'sweetalert2';
import { Comment } from '../../../../core/models/comment.model';
import { NgxSpinnerService } from 'ngx-spinner';
import { ActivatedRoute, Router } from '@angular/router';
import { CopyClaims } from 'app/core/models/copy-claims.model';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

@Component({
  selector: 'app-comment',
  templateUrl: './comment.component.html',
  styleUrls: ['./comment.component.scss']
})
export class CommentComponent implements OnInit {
  @Input() commentS1Application = new S1Application();
  @Input() commentS2Application = new S2Application();
  @Input() sampleDataTransfer;


  comment = new Comment();
  internalComment:CommentThread = new CommentThread();
  externalComment:CommentThread = new CommentThread();
  copyClaims = new CopyClaims();
  commentForm: FormGroup;
  todayDate: Date = new Date();
  commentList = [];
  internalCommentList:Array<CommentThread> = new Array<CommentThread>();
  externalCommentList:Array<CommentThread> = new Array<CommentThread>();
  replyInternalComment:CommentThread = new CommentThread();
  copyClaimsList = [];
  commentId: number = 0;
  commentTypeList = [...COMMENT_TYPE];
  commentType = '';
  applicationStage: string;
  internalCommentProcess = 'Save';
  externalCommentProcess = 'Save';
  copyClaimesProcess = 'Save';
  id: any;
  commentHtmlOutput: string;
  disabledComment = false;

  currentUser: any;
  editCopyClaimsComment = false;
  editExternalComment = false;

  replyEditorConfig: AngularEditorConfig = {
    editable: true,
    spellcheck: true,
    height: '13m',
    minHeight: '0',
    maxHeight: 'auto',
    width: 'auto',
    minWidth: '0',
    translate: 'no',
    enableToolbar: true,
    showToolbar: true,
    placeholder: 'Enter reply here...',
    defaultParagraphSeparator: 'p',
    defaultFontName: 'Calibri',
    defaultFontSize: '3',
    fonts: [
      { class: 'arial', name: 'Arial' },
      { class: 'times-new-roman', name: 'Times New Roman' },
      { class: 'calibri', name: 'Calibri' },
      { class: 'comic-sans-ms', name: 'Comic Sans MS' }
    ],

    customClasses: [
      {
        name: 'quote',
        class: 'quote',
      },
      {
        name: 'redText',
        class: 'redText'
      },
      {
        name: 'titleText',
        class: 'titleText',
        tag: 'h1',
      },
    ],
    uploadUrl: 'v1/image',
    // upload: (file: File) => { ... }
    uploadWithCredentials: false,
    sanitize: true,
    toolbarPosition: 'top',
    toolbarHiddenButtons: [
      ['link',
        'unlink',
        'insertImage',
        'insertVideo',],
    ]
  }

  editorConfig: AngularEditorConfig = {
    editable: true,
    spellcheck: true,
    height: '15em',
    minHeight: '0',
    maxHeight: 'auto',
    width: 'auto',
    minWidth: '0',
    translate: 'no',
    enableToolbar: true,
    showToolbar: true,
    placeholder: 'Enter text here...',
    defaultParagraphSeparator: 'p',
    defaultFontName: 'Calibri',
    defaultFontSize: '3',
    fonts: [
      { class: 'arial', name: 'Arial' },
      { class: 'times-new-roman', name: 'Times New Roman' },
      { class: 'calibri', name: 'Calibri' },
      { class: 'comic-sans-ms', name: 'Comic Sans MS' }
    ],

    customClasses: [
      {
        name: 'quote',
        class: 'quote',
      },
      {
        name: 'redText',
        class: 'redText'
      },
      {
        name: 'titleText',
        class: 'titleText',
        tag: 'h1',
      },
    ],
    uploadUrl: 'v1/image',
    // upload: (file: File) => { ... }
    uploadWithCredentials: false,
    sanitize: true,
    toolbarPosition: 'top',
    toolbarHiddenButtons: [
      ['link',
        'unlink',
        'insertImage',
        'insertVideo',],
    ]
  };

  constructor(
    private formBuilder: FormBuilder,
    private dataStorageService: DataStorageService,
    private sweetAlertService: SweetAlertService,
    private dataStorage: DataStorageService,
    private apiService: ApiService,
    public datepipe: DatePipe,
    private spinner: NgxSpinnerService,
    private route: ActivatedRoute,
    private dialog: MatDialog,
  ) { }

  ngOnInit(): void {

    this.setFormValidator();
    this.id = Number(this.route.snapshot.paramMap.get('id'));
    this.currentUser = this.dataStorage.getUserAccount();
    console.log(this.applicationStage);

    if ((!this.commentS1Application.screenerDecisionComment && !this.commentS1Application.reviewerDecisionComment &&
      (this.commentS1Application.releaseStatus === 'FOR_RELEASE' || this.commentS1Application.releaseStatus === 'RELEASED') &&
      this.currentUser.userRole.name !== 'ROLE_REVIEWER')
      || this.commentS1Application.internalStatus === APPLICATION_STATUS.S1_APPLICATION_REJECTED) {
      this.disabledComment = true;
    }


    // if(this.applicationStage != APPLICATION_STAGE.S2_APPLICATION){
    //     this.getComments('');
    // }
    this.getComments();
    this.getCopyClaims();
  }

  addInternalComment(comment: CommentThread): void {
    let endpoint;
    if (this.applicationStage === APPLICATION_STAGE.S2_APPLICATION) {
      endpoint = ENDPOINTS.commentS2
      this.internalComment.applicationFormType = APPFORM_TYPE.S2;
      this.internalComment.applicationFormS2 = this.commentS2Application;
      console.log('S2 Comment Object', this.internalComment);
    } else {
      endpoint = ENDPOINTS.comment
      this.internalComment.applicationFormType = APPFORM_TYPE.S1;
      this.internalComment.appFormReference = this.commentS1Application.appFormReference;
      console.log('S1 Comment Object', this.internalComment);
    }

    if (this.internalCommentProcess === 'Save') {
      this.spinner.show();
      comment.actionBy = this.currentUser;
      comment.role = this.dataStorageService.getUserRole();
      comment.actionDate = new Date();
      comment.commentType = 'INTERNAL';
      comment.comment = this.setHtml5Value(comment.comment);
      this.internalCommentList.push(comment);

      this.apiService.save(endpoint, comment).subscribe(
        (res: any) => {
          console.log('RES', res);
          if (res !== undefined) {
            this.spinner.hide();
            this.sweetAlertService.customSuccessMessage('Internal Comment has been added!');
            this.internalComment = new CommentThread();
            this.getComments();
            this.getCommentsS2(res.responseData.data.applicationFormS2);
          }
        }, (err: any) => {
          this.spinner.hide();
          console.log('errrrrrrrr:::', err);

          this.sweetAlertService.error(err);
        }, () => {
          this.spinner.hide();
          this.comment = new Comment();
        })

    } else {
      let dateTransformed = this.datepipe.transform(new Date(), 'yyyy-MM-dd')

      Swal.fire({
        text: 'Are you sure you want to update this comment?',
        icon: 'question',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Confirm'
      }).then((result) => {
        if (result.value) {
          this.spinner.show();
          // this.internalComment.modifiedBy = this.currentUser.username
          // this.internalComment.modifiedDate = new Date();
          this.internalComment.comment = this.setHtml5Value(this.internalComment.comment);
          this.apiService.update(endpoint, this.internalComment).subscribe(
            (res: any) => {
              // this.internalCommentList = res.responseData.data;
              this.spinner.hide();
              this.sweetAlertService.customSuccessMessage('Internal Comment has been Updated!');
              this.internalComment = new CommentThread();
              this.internalCommentProcess = 'Save';
              this.getComments();
              this.getCommentsS2(res.responseData.data.applicationFormS2);
            }, (err: any) => {
              this.spinner.hide();
            });
        }

      });
    }

  }

  sendReply(comment: CommentThread){
    
    console.log(this.replyInternalComment)
    // this.replyInternalComment.parent = comment;
    this.replyInternalComment.isReply = true;
    this.replyInternalComment.parentId = comment.id;
    this.addInternalComment(this.replyInternalComment);

  }
  cancelReply(comment: CommentThread){
    comment = new CommentThread();
    comment.replyEditorIsEnabled = false;
  }

  addExternalComment(): void {
    let endpoint;
    if (this.applicationStage === APPLICATION_STAGE.S2_APPLICATION) {
      endpoint = ENDPOINTS.commentS2
      this.externalComment.applicationFormType = APPFORM_TYPE.S2;
      this.externalComment.applicationFormS2 = this.commentS2Application;
    } else {
      endpoint = ENDPOINTS.comment
      this.externalComment.applicationFormType = APPFORM_TYPE.S1;
      this.externalComment.appFormReference = this.commentS1Application.appFormReference;
    }

    if (this.externalCommentProcess === 'Save') {
      this.spinner.show();
      this.externalComment.actionBy = this.currentUser;
      this.externalComment.role = this.dataStorageService.getUserRole();
      this.externalComment.actionDate = new Date();
      this.externalComment.commentType = 'EXTERNAL';
      this.externalComment.comment = this.setHtml5Value(this.externalComment.comment);
      this.externalCommentList.push(this.externalComment);

      this.apiService.save(endpoint, this.externalComment).subscribe(
        (res: any) => {
          if (res !== undefined) {
            this.spinner.hide();
            this.sweetAlertService.customSuccessMessage('External Comment has been added!');
            this.externalComment = new CommentThread();
            this.editExternalComment = false;
            this.getComments();
            this.getCommentsS2(res.responseData.data.applicationFormS2);
          }
        }, (err: any) => {
          this.spinner.hide();
          console.log('errrrrrrrr:::', err);

          this.sweetAlertService.error(err);
        }, () => {
          this.spinner.hide();
          this.comment = new Comment();
        })

    } else {
      let dateTransformed = this.datepipe.transform(new Date(), 'yyyy-MM-dd')

      Swal.fire({
        text: 'Are you sure you want to update this comment?',
        icon: 'question',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Confirm'
      }).then((result) => {
        if (result.value) {
          this.spinner.show();
          // this.externalComment.modifiedBy = this.currentUser.username;
          // this.externalComment.modifiedDate = new Date();
          this.externalComment.comment = this.setHtml5Value(this.externalComment.comment);
          this.apiService.update(endpoint, this.externalComment).subscribe(
            (res: any) => {
              this.spinner.hide();
              // this.externalCommentList = res.responseData.data;
              this.sweetAlertService.customSuccessMessage('External Comment has been Updated!');
              this.externalComment = new CommentThread();
              this.externalCommentProcess = 'Save'
              this.editExternalComment = false;
              this.getComments();
              this.getCommentsS2(res.responseData.data.applicationFormS2);
            }, (err: any) => {
              this.spinner.hide();
            });
        }

      });
    }
  }

  addCopyClaims(): void {
    if (this.copyClaimesProcess === 'Save') {
      this.spinner.show();
      this.copyClaims.actionBy = this.currentUser;
      this.copyClaims.role = this.dataStorageService.getUserRole();
      this.copyClaims.actionDate = new Date();
      this.copyClaims.appFormReference = this.commentS1Application.appFormReference;
      this.copyClaims.description = this.setHtml5Value(this.copyClaims.description);

      this.apiService.save(ENDPOINTS.copyClaims, this.copyClaims).subscribe(
        (res: any) => {
          if (res !== undefined) {
            this.spinner.hide();
            this.sweetAlertService.customSuccessMessage('Copy claims description has been added!');
            this.copyClaims = new CopyClaims();
            this.getCopyClaims();
            this.editCopyClaimsComment = false;
          }
        }, (err: any) => {
          this.spinner.hide();
          // console.log('errrrrrrrr:::', err);

          this.sweetAlertService.error(err);
        }, () => {
          this.spinner.hide();
          this.copyClaims = new CopyClaims();
        })
    } else {
      Swal.fire({
        text: 'Are you sure you want to update copy claim?',
        icon: 'question',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Confirm'
      }).then((result) => {
        if (result.value) {
          this.spinner.show();
          this.copyClaims.description = this.setHtml5Value(this.copyClaims.description);
          this.apiService.update(ENDPOINTS.copyClaims, this.copyClaims).subscribe(
            (res: any) => {
              this.spinner.hide();
              this.sweetAlertService.customSuccessMessage('Copy claims description has been Updated!');
              this.copyClaims = new CopyClaims();
              this.copyClaimesProcess = 'Save'
              this.editCopyClaimsComment = false;
              this.getCopyClaims();

            }, (err: any) => {
              this.spinner.hide();
            });
        }

      });
    }
  }

  getComments(): void { // comments for S1
    if (this.commentS1Application) {
      this.apiService.findByParam(ENDPOINTS.comment + '/app-form-reference', this.commentS1Application.appFormReference).subscribe(
        (res: any) => {
          this.commentList = res.responseData.data;
          // get internal comments
          this.internalCommentList = this.commentList.filter(internalComment => internalComment.commentType === 'INTERNAL');
          console.log('INTERNAL', this.internalCommentList);
          // get external comments
          this.externalCommentList = this.commentList.filter(externalComment => externalComment.commentType === 'EXTERNAL');
          console.log('EXTERNAL', this.externalCommentList);
          this.spinner.hide();
        }, (err: any) => {
        });
    }
  }

  getCommentsS2(data): void { // comments for S2
    if (data) {
      this.apiService.findByParam(ENDPOINTS.commentS2 + '/application-form-s2', data.id).subscribe(
        (res: any) => {
          this.commentList = res.responseData.data;
          // get internal comments
          this.internalCommentList = this.commentList.filter(internalComment => internalComment.commentType === 'INTERNAL');
          // get external comments
          this.externalCommentList = this.commentList.filter(externalComment => externalComment.commentType === 'EXTERNAL');
        }, (err: any) => {
        });
    }
  }

  getCopyClaims(): void {
    this.apiService.findByParam(ENDPOINTS.copyClaims + '/app-form-reference', this.commentS1Application.appFormReference).subscribe(
      (res: any) => {
        this.copyClaimsList = res.responseData.data;
      }, (err: any) => {
      });
  }

  modifyCopyClaims(data) {
    this.copyClaimesProcess = 'Update';
    this.copyClaims = data;
    this.editCopyClaimsComment = true;
    this.getCopyClaims();
  }

  modifyInternalComment(data): void {
    this.internalCommentProcess = 'Update';
    this.internalComment = data;
    this.getComments();
    this.getCommentsS2(data);
  }

  deleteComment(data): void {
    let endpoint;
    if (this.applicationStage === APPLICATION_STAGE.S2_APPLICATION) {
      endpoint = ENDPOINTS.commentS2
    } else {
      endpoint = ENDPOINTS.comment
    }
    Swal.fire({
      text: 'Are you sure you want to delete this comment?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.spinner.show();
        this.apiService.deleteById(endpoint, data.id).subscribe(
          (res: any) => {
            if (data?.applicationFormType === 'S1') {
              this.getComments();
            } else {
              this.getCommentsS2(data);
            }
            this.sweetAlertService.customSuccessMessage('Comment has been Deleted!');
          }, (err: any) => {
            this.spinner.hide()
          });
      }
    });
  }

  deleteCopyClaims(data): void {

    Swal.fire({
      text: 'Are you sure you want to delete this copy claims?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.apiService.deleteById(ENDPOINTS.copyClaims, data.id).subscribe(
          (res: any) => {
            this.sweetAlertService.customSuccessMessage('Copy claim has been Deleted!');
            this.getCopyClaims();
          }, (err: any) => {
          });
      }
    });
  }

  modifyExternalComment(data): void {
    this.externalCommentProcess = 'Update';
    this.externalComment = data;
    this.editExternalComment = true;
    this.getComments();
    this.getCommentsS2(data);
  }

  cancel(): void {
    this.internalComment = new CommentThread();
    this.externalComment = new CommentThread();
    this.copyClaims = new CopyClaims();
    this.internalCommentProcess = 'Save';
    this.externalCommentProcess = 'Save';
    this.copyClaimesProcess = 'Save';
    this.editCopyClaimsComment = false;
    this.editExternalComment = false;
    this.getComments();
    this.getCopyClaims();

  }

  setFormValidator(): void {
    this.commentForm = this.formBuilder.group({
      internalComments: [''],
      externalComments: [''],
      commentType: [''],
      description: [''],
    });
  }

  setHtml5Value(commentBody: any) {
    this.commentHtmlOutput = "<!DOCTYPE html><html lang=" + "en" + "><meta charset=" + "utf-8" + "><body>" + commentBody + "</body></html>"
    return this.commentHtmlOutput;
  }

}
