import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTable, MatTableDataSource } from '@angular/material/table';
import { ApiService, DataStorageService, SweetAlertService } from '@services';
import { ENDPOINTS } from '@shared';
import { ModalSingleApplicationComponent } from './modal-single-application/modal-single-application.component';

@Component({
  selector: 'app-single-application',
  templateUrl: './single-application.component.html',
  styleUrls: ['./single-application.component.scss']
})
export class SingleApplicationComponent implements OnInit {

  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: false }) sort: MatSort;
  @ViewChild(MatTable, { static: false }) table: MatTable<Object>;

  dataSource: MatTableDataSource<any>
  displayedColumns: string[] = ['referenceCode', 'typeOfApplication', 'brand', 'medium', 'applicantCompanyName', 'paymentStatus'];
  paymentList = [];

  currentUser: any;
  constructor(
    private apiService: ApiService,
    private dialog: MatDialog,
    private dataStorage: DataStorageService,
  ) { }

  ngOnInit(): void {
    this.currentUser = this.dataStorage.getUserAccount();
    this.getS1PaymentList();
  }

  getS1PaymentList() {
    this.apiService.findByParam(ENDPOINTS.applicationFormPresentor + this.currentUser.id,
      'payment-status/FOR_PAYMENT/schedule-of-fees').subscribe((res: any) => {
        this.paymentList = res.responseData.data;
        this.dataSource = new MatTableDataSource<any>(this.paymentList);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      });
  }

  openModal(res) {

    const dialogRef = this.dialog.open(ModalSingleApplicationComponent, {
      width: '80%',
      height: 'fit-content',
      maxHeight: '85vh',
      data: res
    });

    dialogRef.afterClosed().subscribe(result => {
      this.ngOnInit();
    });

  }
}
