import { Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChanges, OnDestroy } from '@angular/core';
import { Company, S1Application, S2Application } from '@models';
import { MatTableDataSource } from '@angular/material/table';
import { COMMON_FUNCTION, ENDPOINTS } from '@shared';
import { ApiService, DataStorageService, SweetAlertService } from '@services';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { AddCompanyComponent } from '@company';
import { ActivatedRoute } from '@angular/router';
import { BrandsComponent } from '@maintenance';
import { AddressService } from '../../../../core/services/address/address.service';
import { ProductComponent } from '../../maintenance/product/product.component';
import { MultipleApplication } from 'app/core/models/multiple-application.model';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-client-information',
  templateUrl: './client-information.component.html',
  styleUrls: ['./client-information.component.scss']
})
export class ClientInformationComponent implements OnInit, OnChanges, OnDestroy {
  @Input() s1Application = new S1Application();
  @Input() s2Application = new S2Application();
  @Input() multipleApplication = new MultipleApplication();
  @Input() isAscUser = false;
  @Input() isDisableFieldOnUpdate = false;
  @Input() company = new Company();
  @Output() checkInactiveAffiliation: EventEmitter<any> = new EventEmitter();
  @Output() isInactiveAffiliation: boolean;
  @Output() hasErrorFields: EventEmitter<any> = new EventEmitter();

  memberAffiliationList = [];
  companyList = [];
  brandList = [];
  productList = [];
  // categoryList = [];
  regionsList = [];
  provinceList = [];
  cityMunicipalityList = [];
  barangayList = [];
  countryList = [];

  notInSelection = ['INACTIVE', 'DISAPPROVED'];

  isNewCompany = false;
  showCompanyAddress = false;
  dataSource: MatTableDataSource<S1Application>;
  clientInformationForm: FormGroup;

  applicationStage: string;
  fullAddress: string;
  currentUser: any;
  ngModelValue: any;
  private ngUnsubscribe = new Subject();

  constructor(
    private apiService: ApiService,
    private addressService: AddressService,
    private dialog: MatDialog,
    public dataStorage: DataStorageService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService,
    private route: ActivatedRoute,
  ) { }

  ngOnInit(): void {
    // console.log('Clientinfo');
    this.currentUser = this.dataStorage.getUserAccount();
    this.setFormValidator();
    this.getBrandList();
    this.getCompanyList();
    this.getAffiliationList();
    this.getCountries();
  }

  ngOnChanges(): void {
    if (this.s1Application.brand) {
      this.setProductList();
    }
  }

  setFormValidator(): void {
    this.clientInformationForm = this.formBuilder.group({
      brand: ['', Validators.required],
      product: ['', Validators.required],
      category: ['', Validators.required],
      company: [''],
      companyAddress: [''],
      country: [''],
      companyName: [''],
      membersAffiliation: ['', Validators.required],
    });

    if (this.currentUser?.accountType === 'APPLICANT') {
      this.clientInformationForm.valueChanges.pipe(takeUntil(this.ngUnsubscribe)).subscribe(value => {
        this.checkInvalidFields(value);
      });
    }
  }


  getBrandList() {
    this.apiService.findAll(ENDPOINTS.activeBrands).subscribe((res: any) => {
      this.brandList = res.responseData.data; // .filter((brand) => !this.notInSelection.includes(brand.status));
      this.productList = [];
    }, (err: any) => {
    });
    if (this.s1Application.brand) {
      this.setProductList();
    }
  }

  setBrand() {
    this.setProductList();
    if (this.s1Application?.product?.status !== COMMON_FUNCTION.FOR_APPROVAL) {
      this.s1Application.product = null;
    }
  }

  setProductList() {
    // const endpoint = this.s1Application.brand ? ENDPOINTS.productsByBrand + this.s1Application.brand.id : ENDPOINTS.product;
    this.apiService.findAll(ENDPOINTS.productsByBrand + this.s1Application.brand.id).subscribe((res: any) => {
      this.productList = res.responseData.data.filter((product) =>
        product.status !== COMMON_FUNCTION.DISAPPROVED.toUpperCase()
        && product.status !== COMMON_FUNCTION.INACTIVE.toUpperCase()
      );
    }, (err: any) => {
    });
  }

  getCompanyList() {
    this.apiService.findAll(ENDPOINTS.activeCompanies).subscribe((res: any) => {
      this.companyList = res.responseData.data; // .filter((company) => !this.notInSelection.includes(company.status));
    }, (err: any) => {
    });
  }

  getAffiliationList(): void {
    this.apiService.findAll(ENDPOINTS.activeMembersAffiliation).subscribe((res: any) => {
      this.memberAffiliationList = res.responseData.data;
    }, (err: any) => {
    });
  }

  getCompanyAddress(companyName) {
    this.showCompanyAddress = false;
    this.isNewCompany = false;
    this.apiService.findByParam(ENDPOINTS.company, `custom/search?companyName=${companyName}`).subscribe((res: any) => {
      this.company = res.data[0];
      if (this.company.membersAffiliation.status === 'INACTIVE') {
        this.checkInactiveAffiliationFunction();
        this.sweetAlertService.customErrorMessage(`Cannot proceed with the application. Company's membership affiliation is no longer active. Please contact ASC admin`);
      }

      if (this.company.status === 'FOR_APPROVAL') {
        this.isNewCompany = true;
      }

    }, (err: any) => {
    });
  }

  compareCompanyName(value: any, selected: any): boolean {
    if (value && selected && value.id === selected.id) {
      return value.companyName === selected.companyName;
    }
  }

  compareBrand(value: any, selected: any): boolean {
    if (value && selected) {
      return value.description === selected.description;
    }
  }

  compareMembersAffiliation(value: any, selected: any): boolean {
    if (value && selected) {
      return value.description === selected.description;
    }
  }

  compareProduct(value: any, selected: any): boolean {
    if (value && selected) {
      return value.description === selected.description;
    }
  }

  // compareCategory(value: any, selected: any): boolean {
  //   if (value && selected) {
  //     return value.description === selected.description;
  //   }
  // }

  openNewCompanyModal(status = 'new') {
    let dataForModal: any;
    if (status === 'new') {
      dataForModal = {
        isFromModal: true,
        isUpdate: false,
        country: this.dataStorage.getDefaultCountry()
      }
    } else if (status === 'update' && this.s1Application.company) {
      dataForModal = {
        isFromModal: true,
        isUpdate: true,
        company: this.s1Application.company
      }
    }

    const dialogRef = this.dialog.open(AddCompanyComponent, {
      width: '80%',
      height: '85%',
      data: {
        dataForModal
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      this.isNewCompany = result ? true : false;
      if (result) {
        this.getCompanyList();
        this.company = result;
        this.dataStorage.setS1NewCompany(this.company);
        this.s1Application.company = this.dataStorage.getS1NewCompany();
      }
    });
  }

  openNewBrandModal(status = 'new') {
    let dataForModal: any;
    if (status === 'new') {
      dataForModal = {
        isFromModal: true,
        isUpdate: false,
      }
    } else if (status === 'update') {
      dataForModal = {
        isFromModal: true,
        isUpdate: true,
        brand: this.s1Application.brand
      }
    }

    const dialogRef = this.dialog.open(BrandsComponent, {
      width: '80%',
      height: '85%',
      data: {
        dataForModal
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.getBrandList();
        this.s1Application.brand = this.dataStorage.getS1NewBrand();
        this.s1Application.product = this.s1Application.product;
      }
    });
  }

  openNewProductModal(status = 'new') {
    let dataForModal: any;
    if (status === 'new') {
      dataForModal = {
        isFromModal: true,
        isUpdate: false,
        brand: this.s1Application?.brand,
      }
    } else if (status === 'update') {
      dataForModal = {
        isFromModal: true,
        isUpdate: true,
        product: this.s1Application?.product,
        currentStatus: 'FOR_APPROVAL'
      }
    }

    const dialogRef = this.dialog.open(ProductComponent, {
      width: '80%',
      height: '85%',
      data: {
        dataForModal
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        // this.getBrandList();
        this.setProductList();
        this.s1Application.product = this.dataStorage.getS1NewProduct();
      }
    });
  }

  compareCountry(value: any, selected: any): boolean {
    if (value && selected) {
      return value.code === selected.code;
    }
  }

  getCountries() {
    this.apiService.findAll(ENDPOINTS.countries).subscribe((res: any) => {
      this.countryList = res.responseData.data;
      this.dataStorage.setDefaultCountry(res.responseData.data[0]);
    }, (err: any) => {
    });
  }

  checkInactiveAffiliationFunction() {
    this.isInactiveAffiliation = this.s1Application?.company?.membersAffiliation?.status === 'INACTIVE';
    this.checkInactiveAffiliation.emit();
  }
  
  checkInvalidFields(s1Application: any): void {
    if (s1Application?.company && s1Application?.brand && s1Application?.product) {
      this.hasErrorFields.emit(false)
    } else {
      this.hasErrorFields.emit(true);
    }
  }

  ngOnDestroy(): void {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }
}
