import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { S1Application } from '@models';
import { ApiService } from '@services';
import { APP_DECISION_STATUS, ATTACHMENT_TYPE, SUBMISSION_TYPE } from '@shared';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { ApplicationSingleMedia } from '../../../../core/models/application-single-media.model';
import { AttachmentDocumentComponent } from '../attachment-document/attachment-document.component';

@Component({
  selector: 'app-preview-application',
  templateUrl: './preview-application.component.html',
  styleUrls: ['./preview-application.component.scss']
})
export class PreviewApplicationComponent implements OnInit {
  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: false }) sort: MatSort;
  dataSource: MatTableDataSource<any>;
  displayedColumns: string[] = ['typeOfDocument', 'variant', 'expiresOn', 'basedOn', 'remarks'];
  @ViewChild(AttachmentDocumentComponent, { static: false }) materialAttachmentDocument: AttachmentDocumentComponent
    ;

  @Input() previewS1Application: any;// = new S1Application();
  attachmentDocuments = [];
  @Input() originalAttachmentDocuments = [];
  @Input() revisionAttachmentDocuments = [];
  @Input() complianceAttachmentDocuments = [];
  @Input() applicationSingleMedia = new ApplicationSingleMedia();
  @Input() revisionLetterAttachment: any;
  @Input() multimediaDTO: any = {};

  supportiveDocumentList = [];
  multimediaMovingList = [];
  multimediaStaticList = [];

  constructor(
    private apiService: ApiService
  ) { }

  ngOnInit(): void {
    // console.log('Preview..', this.previewS1Application);
  }


  viewDocument(typeOfDocument) {
    if (this.previewS1Application.submissionType === SUBMISSION_TYPE.ORIGINAL) {
      this.attachmentDocuments = this.originalAttachmentDocuments;
    } else if (this.previewS1Application.submissionType === SUBMISSION_TYPE.REVISION) {
      this.attachmentDocuments = this.revisionLetterAttachment;
    } else if (this.previewS1Application.submissionType === SUBMISSION_TYPE.COMPLIANCE
      || this.previewS1Application.decisionStatus === APP_DECISION_STATUS.INCOMPLETE) {
      this.attachmentDocuments = this.complianceAttachmentDocuments;
    }

    if (typeOfDocument === ATTACHMENT_TYPE.MAIN) {
      const mainDoc = this.attachmentDocuments.filter(res => res.attachmentType === ATTACHMENT_TYPE.MAIN)[0];
      window.open(this.attachmentDocuments[0].fileURL);
    } else if (typeOfDocument === ATTACHMENT_TYPE.SUPPORTIVE) {
      const supportiveDoc = this.attachmentDocuments.filter(res => res.attachmentType === ATTACHMENT_TYPE.SUPPORTIVE)[0];
      window.open(supportiveDoc.fileURL);
    } else if (typeOfDocument === ATTACHMENT_TYPE.REVISION_LETTER) {
      // this.attachmentDocuments.filter(res => res.attachmentType === ATTACHMENT_TYPE.REVISION_LETTER)[0];
      const revisionLetterDoc = this.revisionLetterAttachment;
      window.open(revisionLetterDoc.fileURL);
    }
  }

}
