import { Component, Inject, Input, OnDestroy, OnInit, Optional, ViewChild } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTable, MatTableDataSource } from '@angular/material/table';
import { DomSanitizer } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { AttachmentDocument, Payment, S1Application } from '@models';
import { ApiService, DataStorageService, SweetAlertService } from '@services';
import { ATTACHMENT_TYPE, ENDPOINTS, ERROR_MESSAGES, PAYMENT_STATUS, SUBMISSION_TYPE, USER_ROLES } from '@shared';
import { ApplicationSingleMedia } from 'app/core/models/application-single-media.model';
import { MultipleApplication } from 'app/core/models/multiple-application.model';
import { FileStorageService } from 'app/core/services/file.storage.service';
import Swal from 'sweetalert2';

export interface companyDetails {
  referenceCode: string;
  amount: number;
}

const ELEMENT_DATA: companyDetails[] = [
  { referenceCode: 'Company one ', amount: 3000, },
  { referenceCode: 'Company two ', amount: 4000, },
  { referenceCode: 'Company three ', amount: 5000, },
  { referenceCode: 'Company two ', amount: 6000, },
  { referenceCode: 'Company three ', amount: 7000, },
];

@Component({
  selector: 'app-modal-multiple-applications',
  templateUrl: './modal-multiple-applications.component.html',
  styleUrls: ['./modal-multiple-applications.component.scss']
})
export class ModalMultipleApplicationsComponent implements OnInit, OnDestroy {

  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: false }) sort: MatSort;
  @ViewChild(MatTable, { static: false }) table: MatTable<ModalMultipleApplicationsComponent>;
  dataSource: MatTableDataSource<any>
  displayedColumns: string[] = ['referenceCode', 'adTitle', 'mediumAndLength', 'amount'];
  multipleApplicationList = [];

  @Input() s1Application = new S1Application();
  payment = new Payment();
  safeURLProofOfPayment: any;
  selectedFiles: any;
  previewDocument = false;
  attachmentDocument = new AttachmentDocument();
  applicationSingleMedia = new ApplicationSingleMedia();
  currentUser: any;
  attachmentDocuments = [];
  isSubmitted = false;
  paymentTypeList = [];
  paymentType: any;
  attachment: any;
  multipleApplication = new MultipleApplication();
  totalAmount: any;
  paymentDetails: any;
  isDisabled = false;
  typeOfMediumDisplay = '';

  constructor(
    private sanitizer: DomSanitizer,
    private apiService: ApiService,
    public dataStorage: DataStorageService,
    private fileStorageService: FileStorageService,
    private sweetAlertService: SweetAlertService,
    private router: Router,
    @Optional() public dialogRef: MatDialogRef<ModalMultipleApplicationsComponent>,
    @Optional() @Inject(MAT_DIALOG_DATA) public data: any
  ) {
    if (data) {
      this.multipleApplication = data;
      if (this.multipleApplication.paymentStatus !== PAYMENT_STATUS.FOR_PAYMENT) {
        this.isDisabled = true;
      }
      this.getPaymentDetails();
    }
  }

  ngOnInit(): void {
    this.getMultipleApplicationList();
    this.getPaymentList();
  }

  getPaymentDetails() {
    this.apiService.findAll(ENDPOINTS.multiplePaymentDetails + this.multipleApplication.id).subscribe((res: any) => {
      this.paymentDetails = res;
    });
  }


  getMultipleApplicationList() {
    this.apiService.findAll(ENDPOINTS.getMultipleAppFormsWithNoRejected + this.multipleApplication.id).subscribe((res: any) => {
      this.multipleApplicationList = res.responseData.data;
      console.log(this.multipleApplicationList);
      // this.totalAmount = this.multipleApplicationList.map(mapped => mapped.amount).reduce((previousValue, currentValue) =>
      //   previousValue + currentValue,
      //   0);

      this.multipleApplicationList.forEach((multipleApplication) => {
        switch (multipleApplication?.refTypeOfMedium || multipleApplication?.applicationFormS2?.applicationForm?.refTypeOfMedium) {
          case 'SINGLE MEDIA':
            this.getApplicationSingleMedia(multipleApplication?.appFormReference);
            break;
          case 'MULTIMEDIA-MOVING':
            this.getMultimediaMoving(multipleApplication?.appFormReference);
            break;
          case 'MULTIMEDIA-STATIC':
            this.getMultimediaStatic(multipleApplication?.appFormReference);
            break;
          default:
            break;
        }
      });


      this.dataSource = new MatTableDataSource<any>(this.multipleApplicationList);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;

    }, (err: any) => {
      err.error.message = ERROR_MESSAGES.under_maintenance;
      this.sweetAlertService.error(err);
    });
  }

  getPaymentList() {
    this.apiService.findAll(ENDPOINTS.paymentType).subscribe((res: any) => {
      const currentUser = this.dataStorage.getUserAccount();
      if (currentUser.userRole.name === USER_ROLES.ROLE_APPLICANT) {
        const creditCardIndex = this.paymentTypeList
          .findIndex((element) => element.paymentDescription === 'Credit Card');
        this.paymentTypeList.splice(creditCardIndex, 1);
      }
      this.paymentTypeList = res.responseData.data;
    });
  }

  selectFile(event) {
    const attachmentSize = event.target.files[0].size;

    if (event.target.files[0].type === 'application/pdf') {
      if (attachmentSize > 15000000) { // 1MB = 1M
        this.sweetAlertService.customErrorMessage(`Attachments' total file size should not exceed 15MB.`);
        event.target.value = '';
        return;
      } else {
        const pdfURL = URL.createObjectURL(event.target.files[0]);
        this.safeURLProofOfPayment = this.sanitizer.bypassSecurityTrustResourceUrl(pdfURL);
        this.previewDocument = true;

        // env.showSpinner = true;
        const reader = new FileReader();
        const [file] = event.target.files;

        reader.readAsDataURL(file);
        reader.onload = () => {
        };
      }

      this.selectedFiles = event.target.files;
    } else {
      this.sweetAlertService.customErrorMessage('Attachment should be in PDF format only.');
      event.target.value = '';
      return;
    }
  }

  uploadDocument(multipleApplication) {
    Swal.fire({
      text: 'Are you sure you want to submit this PROOF OF PAYMENT?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.dataStorage.setDisplayProgressbar(true);
        let params = '';
        params = '?&attachmentType=' + ATTACHMENT_TYPE.PAYMENT + '&submissionType=' + SUBMISSION_TYPE.ORIGINAL;
        this.fileStorageService.pushFileToStorage(ENDPOINTS.attachmentDocument, this.selectedFiles.item(0), params)
          .subscribe(res => {
            const obj = res.responseData.data;
            console.log(obj);
            this.s1Application.paymentAttachmentReference = obj.paymentAttachmentReference;
            this.payment.paymentAttachmentReference = obj.paymentAttachmentReference;
            this.payment.amountDue = this.totalAmount;
            this.payment.multipleApplication = multipleApplication;
            this.payment.filename = obj.filename;
            this.saveProofOfPayment();
            this.selectedFiles = undefined;
          }, (err: any) => {
            this.sweetAlertService.error(err);
          }, () => {
            // actions after subscription
            this.dataStorage.setDisplayProgressbar(false);
          });
      }
    });
  }

  saveProofOfPayment() {
    console.log('Saving payment...');
    this.dataStorage.setDisplayProgressbar(true);
    this.payment.paymentStatus = PAYMENT_STATUS.FOR_APPROVAL;
    this.payment.id = 0;
    this.payment.officialReceiptNo = '';
    this.payment.paymentMethod = null;
    this.payment.amountDue = this.multipleApplication.totalAmount;
    console.log('PAYMENT : ', this.payment);
    console.log(this.paymentType.id);


    this.apiService.save(`${ENDPOINTS.applicationFormPresentor}payment-type-id/${this.paymentType.id}/payment-multiple/submit`, this.payment)
      .subscribe((res: any) => {
        console.log('SAVED STATUS: ', res.responseData.data);

        this.safeURLProofOfPayment = this.sanitizer.bypassSecurityTrustResourceUrl(res.responseData.data.fileURL);
        this.isSubmitted = true;
        this.sweetAlertService.customSuccessMessage('Proof of payment successfully submitted.');
        this.closeModal();
      }, (err) => {
        console.log(err);
        this.sweetAlertService.error(err);
        this.dataStorage.setDisplayProgressbar(false);
      }, () => {
        this.dataStorage.setDisplayProgressbar(false);
      });
  }

  closeModal(): void {
    this.dialogRef.close();
    this.router.navigate(['asc/page/application/multiple-applications']);
  }
  ngOnDestroy(): void {
    // this.s1Application = null;
  }

  getApplicationSingleMedia(appFormReference = this.payment?.applicationForm?.appFormReference) {
    if (this.payment?.applicationFormS2?.applicationForm?.refTypeOfMedium) {
      appFormReference = this.payment?.applicationFormS2?.applicationForm?.appFormReference;
    }
    this.apiService.findByParam(ENDPOINTS.applicationSingleMedia,
      `app-form-reference/${appFormReference}`).subscribe(
        (res: any) => {
          if (res.responseData.data.length > 0) {
            this.applicationSingleMedia = res.responseData.data[0];

            this.typeOfMediumDisplay = `${this.applicationSingleMedia?.mediumExecution?.typeOfMedium?.description} (${this.applicationSingleMedia?.sizeLength})`;
            console.log('view applicationSingleMedia: ', this.typeOfMediumDisplay);

            this.multipleApplicationList.forEach((element, index) => {
              if (element.appFormReference === appFormReference) {
                this.multipleApplicationList[index] = {
                  ...this.multipleApplicationList[index],
                  typeOfMediumDisplay: this.typeOfMediumDisplay
                };
              }
            });
            this.dataSource = new MatTableDataSource<any>(this.multipleApplicationList);
            this.dataSource.paginator = this.paginator;

          } else {
            this.applicationSingleMedia = new ApplicationSingleMedia();
            this.typeOfMediumDisplay = '';
          }
        }, (err: any) => {
          this.applicationSingleMedia = new ApplicationSingleMedia();
          this.typeOfMediumDisplay = '';
        });
  }

  getMultimediaStatic(appFormReference = this.payment?.applicationForm?.appFormReference) {
    this.apiService.findByParam(ENDPOINTS.applicationMultimedia,
      `app-form-reference/${appFormReference}`).subscribe(
        (res: any) => {
          if (res.responseData.data.length > 0) {
            const multimediaList = new Set(res.responseData.data.map(mappedList => `${mappedList?.typeOfMedium?.description} (${mappedList?.multimediaSizeLength})`));

            this.typeOfMediumDisplay = '';
            let mediaCtrl = 1;
            for (const media of multimediaList) {
              if (mediaCtrl !== multimediaList.size) {
                this.typeOfMediumDisplay += `${media}, `;
                mediaCtrl++;
              } else {
                this.typeOfMediumDisplay += media;
              }
            }

            this.multipleApplicationList.forEach((element, index) => {
              console.log(element);
              if (element.appFormReference === appFormReference) {
                this.multipleApplicationList[index] = {
                  ...this.multipleApplicationList[index],
                  typeOfMediumDisplay: this.typeOfMediumDisplay
                };
              }
            });
            this.dataSource = new MatTableDataSource<any>(this.multipleApplicationList);
            this.dataSource.paginator = this.paginator;

          } else {
            this.typeOfMediumDisplay = '';
          }
        }, (err: any) => {
          this.typeOfMediumDisplay = '';
        });
  }

  getMultimediaMoving(appFormReference = this.payment?.applicationForm?.appFormReference) {
    this.apiService.findByParam(ENDPOINTS.applicationMultimedia,
      `app-form-reference/${appFormReference}`).subscribe(
        (res: any) => {
          if (res.responseData.data.length > 0) {
            const multimediaList = new Set(res.responseData.data.map(mappedList => `${mappedList?.typeOfMedium?.description} (${mappedList?.multimediaSizeLength})`));

            this.typeOfMediumDisplay = '';
            let mediaCtrl = 1;
            for (const media of multimediaList) {
              if (mediaCtrl !== multimediaList.size) {
                this.typeOfMediumDisplay += `${media}, `;
                mediaCtrl++;
              } else {
                this.typeOfMediumDisplay += media;
              }
            }

            this.multipleApplicationList.forEach((element, index) => {
              console.log(element);
              if (element.appFormReference === appFormReference) {
                this.multipleApplicationList[index] = {
                  ...this.multipleApplicationList[index],
                  typeOfMediumDisplay: this.typeOfMediumDisplay
                };
              }
            });
            this.dataSource = new MatTableDataSource<any>(this.multipleApplicationList);
            this.dataSource.paginator = this.paginator;

          } else {
            this.typeOfMediumDisplay = '';
          }
        }, (err: any) => {
          this.typeOfMediumDisplay = '';
        });
  }
}
