import { Component, ElementRef, Inject, OnInit, Optional, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { S1Application } from '@models';
import { ApiService, DataStorageService } from '@services';
import { APPFORM_TYPE, ENDPOINTS, FORM_TYPE } from '@shared';
import { NumberOfMultipleApplicationAllowed } from 'app/core/models/numberOfMultipleApplicationAllowed.model';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-s1-form-type',
  templateUrl: './s1-form-type.component.html',
  styleUrls: ['./s1-form-type.component.scss']
})
export class S1FormTypeComponent implements OnInit {

  s1Application = new S1Application();
  numberOfMultipleApplicationAllowed = new NumberOfMultipleApplicationAllowed();
  applicationTypeList = [];
  formTypeList = [...FORM_TYPE];

  s1ApplicationForm: FormGroup;

  numberOfApplications: number;
  showNumberOfApplications = false;
  isMultiple = false;
  isSpecial = false;
  isPresentation = false;

  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private router: Router,
    private dataStorage: DataStorageService,
    private dialog: MatDialog,
    @Optional() public dialogRef: MatDialogRef<S1FormTypeComponent>,
    @Optional() @Inject(MAT_DIALOG_DATA) public data: any
  ) { }

  ngOnInit(): void {
    this.setFormValidator('');
    this.getApplicationTypeList();
    this.s1Application.formType = 'INDIVIDUAL';
    this.getNoOfMultipleApplicationsAllowed();
  }

  getNoOfMultipleApplicationsAllowed(): void {
    this.apiService.findAll(ENDPOINTS.noOfMultipleApplicationAllowed).subscribe((res: any) => {
      this.numberOfMultipleApplicationAllowed = res.responseData.data[0];
    });
  }

  getApplicationTypeList() {
    this.s1Application.applicationType = null;
    this.applicationTypeList = [];
    this.apiService.findAll(ENDPOINTS.applicationType).subscribe((res: any) => {
      this.applicationTypeList = res.responseData.data;
    });
  }

  setAsMultiple() {
    this.isMultiple = !this.isMultiple;
    this.showNumberOfApplications = !this.showNumberOfApplications;
    if (this.isMultiple) {
      this.s1Application.formType = APPFORM_TYPE.MULTIPLE;
      this.setFormValidator('multiple')
    } else {
      this.setFormValidator('');
      this.s1Application.formType = APPFORM_TYPE.INDIVIDUAL;
    }
  }

  checkType() {
    if (this.s1Application.applicationType.description.toLocaleLowerCase().includes('special')) {
      this.isSpecial = true;
    } else {
      this.isSpecial = false;
    }

    if (this.isMultiple) {
      this.setFormValidator('multiple');
    }

  }

  setFormValidator(apptype): void {
    let formtype;
    if (this.s1Application.formType) {
      formtype = this.s1Application.formType;
    }

    if (apptype === 'multiple') {
      this.s1ApplicationForm = this.formBuilder.group({
        formType: [formtype, Validators.required],
        applicationType: [this.s1Application.applicationType, Validators.required],
        numberOfApplications: [
          '', [
            Validators.required,
            Validators.min(parseInt(this.numberOfMultipleApplicationAllowed.minimum.toString(), null)),
            Validators.max(parseInt(this.numberOfMultipleApplicationAllowed.maximum.toString(), null))
          ]
        ],
      });
    } else {
      this.s1ApplicationForm = this.formBuilder.group({
        applicationType: [this.s1Application.applicationType, Validators.required],
      });
      this.showNumberOfApplications = false;
    }
  }

  proceedS1Application(): void {
    if (!this.isMultiple) {
      Swal.fire({
        text: 'Note: The application to be created is an individual application',
        icon: 'info',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'PROCEED'
      }).then((result) => {
        if (result.value) {
          this.navigateToS1Application();
        }
      });
      return;
    }

    this.navigateToS1Application();
  }

  navigateToS1Application(): void {
    this.router.navigate(['/asc/page/application/s1/add']);
    const formAndAppType = {
      formType: this.s1Application.formType,
      appType: this.s1Application.applicationType
    }

    this.dataStorage.setS1FormAndAppType(formAndAppType);
    this.dataStorage.setNumberOfApplications(this.numberOfApplications);
    this.closeModal();
  }

  setAsForPresentation() {
    this.isPresentation = !this.isPresentation;
    if (this.isPresentation) {
      Swal.fire({
        text: 'Note: Please be present or available during presentation period.',
        icon: 'info',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'PROCEED'
      }).then((result) => {
        if (result.value) {
          this.s1Application.formType = APPFORM_TYPE.FOR_PRESENTATION;
          this.proceedS1Application();
        }
      });
    }
  }

  closeModal(): void {
    this.dialogRef.close();
  }

}
