  onChangeDate(value) {
    console.log(value);
    if (value) {
      const dateFetched = value;
      const dateTransformed = this.datepipe.transform(
        dateFetched,
        "yyyy-MM-dd"
      );
      this.s1Application.submissionDate = new Date(dateTransformed);
      this.s1Application.autoSubmit = true;
      this.disableSaveAndSubmitButton();
    }
  }

  resendDecision() {
    Swal.fire({
      text: "Are you sure you want to resend decision form?",
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Confirm",
    }).then((result) => {
      if (result.value) {
        this.spinner.show();
        this.apiService
          .save(
            ENDPOINTS.api +
              `admin/application-form/id/${this.s1Application.id}/send/decision-form`,
            this.s1Application
          )
          .subscribe(
            (res: any) => {
              if (res.code === 200) {
                this.spinner.hide();
                this.sweetAlertService.customSuccessMessage(
                  "Successfully resend decision form"
                );
                this.navigateToS1List();
              }
            },
            (err: any) => {
              this.spinner.hide();
            }
          );
      }
    });
  }

  returnToScreener() {
    Swal.fire({
      text: "Are you sure you want to return this application to Screener?",
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Confirm",
    }).then((result) => {
      if (result.value) {
        this.spinner.show();
        let endpoint = "";
        if (this.currentUser.userRole.name === USER_ROLES.ROLE_AD_SPECIALIST) {
          endpoint =
            ENDPOINTS.api +
            `application-form/id/${this.s1Application.id}/s1/ad-specialist/forward-to-screener`;
        } else if (
          this.currentUser.userRole.name === USER_ROLES.ROLE_ADMIN ||
          this.currentUser.userRole.name === USER_ROLES.ROLE_SUPER_ADMIN
        ) {
          endpoint =
            ENDPOINTS.api +
            `admin/application-form/id/${this.s1Application.id}/forward-to-screener`;
        }

        this.apiService.save(endpoint, this.s1Application).subscribe(
          (res: any) => {
            if (res.code === 200) {
              console.log("RETURN: ", res.responseData.data);
              this.spinner.hide();
              this.sweetAlertService.customSuccessMessage(
                "Successfully return to Screener"
              );
              this.navigateToS1List();
            }
          },
          (err: any) => {
            this.spinner.hide();
          }
        );
      }
    });
  }

  returnTo(ascUser, newStatus): void {
    Swal.fire({
      text:
        "Are you sure you want to return this application to " + ascUser + "?",
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Confirm",
    }).then((result) => {
      if (result.value) {
        this.spinner.show();
        let endpoint = "";
        this.s1Application.assignedUser = this.s1Application.actionBy;
        this.s1Application.actionBy = this.currentUser;

        if (this.currentUser.userRole.name === USER_ROLES.ROLE_SCREENER) {
          if (
            this.s1Application.multipleApplication &&
            this.s1Application.submissionType === SUBMISSION_TYPE.ORIGINAL
          ) {
            endpoint = `${ENDPOINTS.multipleApplicationReturnToAdSpe}${this.s1Application.multipleApplication.id}/return`;
          } else {
            endpoint = `${ENDPOINTS.applicationFormScreener}return`;
          }
        } else if (
          this.currentUser.userRole.name === USER_ROLES.ROLE_REVIEWER
        ) {
          if (
            this.s1Application.multipleApplication &&
            this.s1Application.submissionType === SUBMISSION_TYPE.ORIGINAL
          ) {
            endpoint = `${ENDPOINTS.multipleApplicationReturnToScreener}${this.s1Application.multipleApplication.id}/return`;
          } else {
            endpoint = `${ENDPOINTS.applicationFormReviewer}return`;
          }
        }

        // console.log('TO SAVE: ', this.s1Application);
        this.apiService.save(endpoint, this.s1Application).subscribe(
          (res: any) => {
            if (res.code === 200) {
              // console.log('RETURN: ', res.responseData.data);
              this.spinner.hide();
              this.sweetAlertService.customSuccessMessage(
                "Successfully returned the application"
              );
              this.navigateToS1List();
            }
          },
          (err: any) => {
            this.spinner.hide();
          }
        );
      }
    });
  }

  reassign(): void {
    if (
      this.s1Application.internalStatus ===
      APPLICATION_STATUS.S1_APPLICATION_VERIFIED_BY_AD_SPE
    ) {
      if (this.s1Application.multipleApplication) {
        this.s1Application.multipleApplication.assignedScreener = this.s1Application.assignedScreener;
      } else {
        this.s1Application.assignedUser = this.s1Application.assignedScreener;
      }
    } else if (
      this.s1Application.internalStatus ===
      APPLICATION_STATUS.S1_APPLICATION_SCREENED
    ) {
      if (this.s1Application.multipleApplication) {
        this.s1Application.multipleApplication.assignedReviewer = this.s1Application.assignedReviewer;
      } else {
        this.s1Application.assignedUser = this.s1Application.assignedReviewer;
      }
    }

    Swal.fire({
      text: "Are you sure you want to reassign this application?",
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Confirm",
    }).then((result) => {
      if (result.value) {
        this.spinner.show();
        let endpoint = "";
        let param = {};
        this.s1Application.actionBy = this.currentUser;

        if (this.currentUserRole === USER_ROLES.ROLE_AD_SPECIALIST) {
          if (this.s1Application.multipleApplication) {
            endpoint = ENDPOINTS.reAssignScreenerForMultipleApplication;
            param = this.s1Application.multipleApplication;
          } else {
            endpoint = `${ENDPOINTS.applicationFormAdSpecialist}re-assign/screener`;
            param = this.s1Application;
          }
        } else {
          if (this.s1Application.multipleApplication) {
            endpoint = ENDPOINTS.reAssignReviewerForMultipleApplication;
            param = this.s1Application.multipleApplication;
          } else {
            endpoint = `${ENDPOINTS.applicationFormScreener}re-assign/reviewer`;
            param = this.s1Application;
          }
        }

        this.apiService.save(endpoint, param).subscribe(
          (res: any) => {
            console.log("SAVED: ", res.responseData.data);
            if (res.code === 200) {
              this.spinner.hide();
              this.sweetAlertService.success(res);
              this.navigateToS1List();
            }
          },
          (err: any) => {
            this.spinner.hide();
          }
        );
      }
    });
  }

