import { DatePipe } from "@angular/common";
import { Component, Inject, OnInit, Optional, ViewChild } from "@angular/core";
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { Router } from "@angular/router";
import { Company, S1Application } from "@models";
import { ApiService, DataStorageService, SweetAlertService } from "@services";
import {
  APPFORM_TYPE,
  ENDPOINTS,
  ERROR_MESSAGES,
  FORM_TYPE,
  ROLES,
  USER_ROLES,
} from "@shared";
import { ApplicationSingleMedia } from "app/core/models/application-single-media.model";
import { MultipleApplication } from "app/core/models/multiple-application.model";
import { NgxSpinnerService } from "ngx-spinner";
import Swal from "sweetalert2";

@Component({
  selector: "app-update-multiple-application",
  templateUrl: "./update-multiple-application.component.html",
  styleUrls: ["./update-multiple-application.component.scss"],
})
export class UpdateMultipleApplicationComponent implements OnInit {
  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: false }) sort: MatSort;

  company = new Company();
  s1Application = new S1Application();
  multipleS1Application = new MultipleApplication();
  applicationSingleMedia = new ApplicationSingleMedia();
  multipleS1ApplicationList = [];
  dataSource: MatTableDataSource<S1Application>;
  displayedColumns: string[] = [
    "referenceCode",
    "typeOfMediumDisplay",
    "mainAttachmentReference",
    "supportingAttachmentReference",
    "brand",
    "status",
    "decisionStatus",
  ];

  currentUser: any;
  currentUserRole: any;

  isAscUser = false;
  isDisableFieldOnUpdate = true;
  isDisabledSubmitButton = true;
  canForwardMultipleApp = false;
  isAllApplicationsHasMainDoc = false;
  warningNote =
    "Note: To submit multiple application, please make sure that every application has complete details and has necessary attachments.";
  typeOfMediumDisplay = "";

  formType = APPFORM_TYPE.MULTIPLE;

  ascUserRoleList = [
    ...ROLES.filter((role: any) => role.ascUser === true).map(
      (role: any) => role.role
    ),
  ];

  constructor(
    private apiService: ApiService,
    private sweetAlertService: SweetAlertService,
    private dataStorage: DataStorageService,
    private router: Router,
    private spinner: NgxSpinnerService,
    private datepipe: DatePipe,
    @Optional()
    public dialogRef: MatDialogRef<UpdateMultipleApplicationComponent>,
    @Optional() @Inject(MAT_DIALOG_DATA) public data: any
  ) {
    if (data) {
      this.multipleS1Application = data;
      this.s1Application.formType = this.formType;

      // Client Info
      this.s1Application.company = this.multipleS1Application.company;
      this.company = this.multipleS1Application.company;
      // this.s1Application.membersAffiliation = this.multipleS1Application.membersAffiliation;
      this.s1Application.brand = this.multipleS1Application.brand;
      this.s1Application.product = this.multipleS1Application.product;
      // this.s1Application.category = this.multipleS1Application.category;
    }
  }

  ngOnInit(): void {
    this.currentUser = this.dataStorage.getUserAccount();
    this.currentUserRole = this.currentUser.userRole.name;
    this.isAscUser = this.ascUserRoleList.includes(
      this.dataStorage.getUserRole()
    );
    this.getS1FormsByMultipleApplication();
    if (this.currentUserRole !== USER_ROLES.ROLE_APPLICANT) {
      this.displayedColumns = [
        "referenceCode",
        "typeOfMediumDisplay",
        "mainAttachmentReference",
        "supportingAttachmentReference",
        "brand",
        "status",
        "ASCstatus",
        "decisionStatus",
      ];
    }
  }

  getS1FormsByMultipleApplication() {
    let endpoint = "";
    if (
      this.currentUserRole === USER_ROLES.ROLE_APPLICANT ||
      this.currentUserRole === USER_ROLES.ROLE_ADMIN ||
      this.currentUserRole === USER_ROLES.ROLE_SUPER_ADMIN
    ) {
      endpoint = ENDPOINTS.getMultipleAppForms + this.multipleS1Application.id;
    } else {
      endpoint =
        ENDPOINTS.getMultipleAppFormsWithNoRejected +
        this.multipleS1Application.id;
    }
    // this.apiService.findAll(ENDPOINTS.applicationFormPresentor + `multiple-application/${this.multipleS1Application.id}/applications`)
    this.apiService.findAll(endpoint).subscribe(
      (res: any) => {
        this.multipleS1ApplicationList = res.responseData.data;

        this.isAllApplicationsHasMainDoc = this.multipleS1ApplicationList.filter(
          (filteredList) => !filteredList.mainAttachmentReference
        ).length
          ? false
          : true;

        this.multipleS1ApplicationList.forEach((value, index) => {
          this.s1Application = value;
          this.multipleS1ApplicationList[index] = value;
          switch (
            value?.refTypeOfMedium ||
            value?.applicationFormS2?.applicationForm?.refTypeOfMedium
          ) {
            case "SINGLE MEDIA":
              this.getApplicationSingleMedia();
              break;
            case "MULTIMEDIA-MOVING":
              this.getMultimediaMoving();
              break;
            case "MULTIMEDIA-STATIC":
              this.getMultimediaStatic();
              break;
            default:
              break;
          }
        });

        this.dataSource = new MatTableDataSource<S1Application>(
          this.multipleS1ApplicationList
        );
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;

        // will be disabled if there's an application without main doc uploaded
        this.isDisabledSubmitButton = this.multipleS1ApplicationList.filter(
          (filteredList) => !filteredList.mainAttachmentReference
        ).length
          ? true
          : false;

        this.isDisabledSubmitButton = this.multipleS1ApplicationList.filter(
          (filteredList) =>
            !filteredList.supportingAttachmentReference &&
            !filteredList.hasNoSupportingDocs
        ).length
          ? true
          : false;

        // if earlier or later than the approved date

        const today = this.datepipe.transform(new Date(), "yyyy-MM-dd");
        const approvedDate = this.datepipe.transform(
          this.multipleS1Application.dateOfSubmission,
          "yyyy-MM-dd"
        );

        if (
          this.multipleS1Application.submissionStatus !== "PROCESSED" &&
          approvedDate !== today &&
          this.multipleS1Application.status !== "DISAPPROVED"
        ) {
          this.isDisabledSubmitButton = true;
          this.warningNote =
            "Note: Applicant can only submit multiple application on approved date.";
        }

        this.checkForwardStatus();
      },
      (err: any) => {
        err.error.message = ERROR_MESSAGES.under_maintenance;
        this.sweetAlertService.error(err);
      }
    );
  }

  checkForwardStatus() {
    if (
      this.multipleS1Application.countVerifiedApplicationForm ===
      this.multipleS1Application.actualNumberOfApplicationForm
    ) {
      this.canForwardMultipleApp = true;
    }
  }

  viewApplication(data) {
    this.dialogRef.close();
    this.router.navigate([`asc/page/application/s1/update/${data.id}`]);
  }

  onApproveOrDisapprove(status = "APPROVED"): void {
    const displayStatus = status === "APPROVED" ? "approve" : "disapprove";
    Swal.fire({
      text: `Are you sure you want to ${displayStatus} this application?`,
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Confirm",
    }).then((result) => {
      if (result.value) {
        const endpointExtension =
          status === "APPROVED" ? "/approve" : "/disapprove";
        this.multipleS1Application.actionDate = new Date();
        this.multipleS1Application.actionBy = this.currentUser.username;
        this.apiService
          .update(
            `${ENDPOINTS.multipleApplication}${endpointExtension}`,
            this.multipleS1Application
          )
          .subscribe(
            (res: any) => {
              this.sweetAlertService.customSuccessMessage(
                "Multiple application submission request has been " +
                  displayStatus +
                  "d."
              );
            },
            (err: any) => {
              err.error.message = ERROR_MESSAGES.under_maintenance;
              this.sweetAlertService.error(err);
            },
            () => {
              this.dialogRef.close(this.multipleS1Application);
            }
          );
      }
    });
  }

  updateMultipleApplications(multipleId): void {
    // TO DO
    // will only redirect to add new S1 application
    // saving or updating is in add-s1-application.component.ts
    const ctr =
      this.multipleS1ApplicationList.length === 0
        ? 1
        : this.multipleS1ApplicationList.length + 1;

    this.dialogRef.close();
    this.router.navigate([
      `asc/page/application/s1/add/multiple/${multipleId}/${ctr}`,
    ]);
  }

  compareProduct(value: any, selected: any): boolean {
    if (value && selected) {
      return value.description === selected.description;
    }
  }

  // compareCategory(value: any, selected: any): boolean {
  //   if (value && selected) {
  //     return value.description === selected.description;
  //   }
  // }

  submitMultiple() {
    Swal.fire({
      text: `Are you sure you want to submit this multiple application?`,
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Confirm",
    }).then((result) => {
      if (result.value) {
        if (
          this.multipleS1Application.numberOfApplication >
          this.multipleS1ApplicationList.length
        ) {
          Swal.fire({
            text: `Are you sure you want to submit ${this.multipleS1ApplicationList.length} out ${this.multipleS1Application.numberOfApplication} applications only?`,
            icon: "question",
            showCancelButton: true,
            confirmButtonColor: "#3085d6",
            cancelButtonColor: "#d33",
            confirmButtonText: "Confirm",
          }).then((result2) => {
            if (result2.value) {
              const endpoint = `${ENDPOINTS.applicationFormPresentor}group-id/${this.multipleS1Application.id}/application-type-id/
                ${this.multipleS1Application.applicationType.id}/batch-multiple-app-form/submit`;
              this.apiService.save(endpoint, null).subscribe(
                (res: any) => {
                  this.sweetAlertService.customSuccessMessage(
                    "Multiple application successfully submitted."
                  );
                  this.getS1FormsByMultipleApplication();
                },
                (err: any) => {
                  err.error.message = ERROR_MESSAGES.under_maintenance;
                  this.sweetAlertService.error(err);
                },
                () => {
                  this.dialogRef.close(this.multipleS1Application);
                }
              );
            }
          });
        } else {
          const endpoint = `${ENDPOINTS.applicationFormPresentor}group-id/${this.multipleS1Application.id}/application-type-id/
        ${this.multipleS1Application.applicationType.id}/batch-multiple-app-form/submit`;
          this.apiService.save(endpoint, null).subscribe(
            (res: any) => {
              this.sweetAlertService.customSuccessMessage(
                "Multiple application successfully submitted."
              );
              this.getS1FormsByMultipleApplication();
            },
            (err: any) => {
              err.error.message = ERROR_MESSAGES.under_maintenance;
              this.sweetAlertService.error(err);
            },
            () => {
              this.dialogRef.close(this.multipleS1Application);
            }
          );
        }
      }
    });
  }

  forwardMultipleAppsTo(user) {
    Swal.fire({
      text: `Are you sure you want to forward this multiple application?`,
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Confirm",
    }).then((result) => {
      if (result.value) {
        this.spinner.show();
        let endpoint = "";

        if (user === "encoder") {
          this.multipleS1Application.assignedEncoder = this.multipleS1ApplicationList[0].assignedEncoder;
          // endpoint = ENDPOINTS.forwardMultipleAppFormToAdSpe;
          endpoint = `${ENDPOINTS.s1ApplicationForm}encoder/group-id/${this.multipleS1Application.id}/forward-multiple-app-form`;
        } else if (user === "ad-specialist") {
          this.multipleS1Application.assignedAdSpecialist = this.multipleS1ApplicationList[0].assignedAdSpecialist;
          this.multipleS1Application.assignedScreener = this.multipleS1ApplicationList[0].assignedScreener;
          endpoint = ENDPOINTS.forwardMultipleAppFormToScreener;
        } else if (user === "screener") {
          this.multipleS1Application.assignedReviewer = this.multipleS1ApplicationList[0].assignedReviewer;
          endpoint = ENDPOINTS.forwardMultipleAppFormToReviewer;
        }

        this.apiService.save(endpoint, this.multipleS1Application).subscribe(
          (res: any) => {
            console.log(res);
            this.spinner.hide();
            this.sweetAlertService.customSuccessMessage(
              "Multiple application successfully forwarded to the next user."
            );
          },
          (err: any) => {
            this.spinner.hide();
            err.error.message = ERROR_MESSAGES.under_maintenance;
            this.sweetAlertService.error(err);
          },
          () => {
            this.spinner.hide();
            this.dialogRef.close(this.multipleS1Application);
          }
        );
      }
    });
  }

  getApplicationSingleMedia(
    appFormReference = this.s1Application?.appFormReference
  ) {
    this.apiService
      .findByParam(
        ENDPOINTS.applicationSingleMedia,
        `app-form-reference/${appFormReference}`
      )
      .subscribe(
        (res: any) => {
          if (res.responseData.data.length > 0) {
            this.applicationSingleMedia = res.responseData.data[0];

            this.typeOfMediumDisplay = `${this.applicationSingleMedia?.mediumExecution?.typeOfMedium?.description} (${this.applicationSingleMedia?.sizeLength})`;

            this.multipleS1ApplicationList.forEach((element, index) => {
              if (element.appFormReference === appFormReference) {
                this.multipleS1ApplicationList[index] = {
                  ...this.multipleS1ApplicationList[index],
                  typeOfMediumDisplay:
                    this.typeOfMediumDisplay === "undefined (0)"
                      ? ""
                      : this.typeOfMediumDisplay,
                };
              }
            });

            this.isDisabledSubmitButton = this.multipleS1ApplicationList.filter(
              (filteredS1App) => filteredS1App.typeOfMediumDisplay === ""
            ).length
              ? true
              : false;

            this.dataSource = new MatTableDataSource<any>(
              this.multipleS1ApplicationList
            );
            this.dataSource.paginator = this.paginator;
          } else {
            this.applicationSingleMedia = new ApplicationSingleMedia();
            this.typeOfMediumDisplay = "";
          }
        },
        (err: any) => {
          this.applicationSingleMedia = new ApplicationSingleMedia();
          this.typeOfMediumDisplay = "";
        }
      );
  }

  getMultimediaStatic(appFormReference = this.s1Application?.appFormReference) {
    this.apiService
      .findByParam(
        ENDPOINTS.applicationMultimedia,
        `app-form-reference/${appFormReference}`
      )
      .subscribe(
        (res: any) => {
          if (res.responseData.data.length > 0) {
            const multimediaList = new Set(
              res.responseData.data.map(
                (mappedList) =>
                  `${mappedList?.typeOfMedium?.description} (${mappedList?.multimediaSizeLength})`
              )
            );

            this.typeOfMediumDisplay = "";
            let mediaCtrl = 1;
            for (const media of multimediaList) {
              if (mediaCtrl !== multimediaList.size) {
                this.typeOfMediumDisplay += `${media}, `;
                mediaCtrl++;
              } else {
                this.typeOfMediumDisplay += media;
              }
            }

            this.multipleS1ApplicationList.forEach((element, index) => {
              if (element.appFormReference === appFormReference) {
                this.multipleS1ApplicationList[index] = {
                  ...this.multipleS1ApplicationList[index],
                  typeOfMediumDisplay: this.typeOfMediumDisplay,
                };
              }
            });
            this.dataSource = new MatTableDataSource<any>(
              this.multipleS1ApplicationList
            );
            this.dataSource.paginator = this.paginator;

            this.isDisabledSubmitButton = this.multipleS1ApplicationList.filter(
              (filteredList) => !filteredList.typeOfMediumDisplay
            ).length
              ? true
              : false;
          } else {
            this.typeOfMediumDisplay = "";
          }
        },
        (err: any) => {
          this.typeOfMediumDisplay = "";
        }
      );
  }

  getMultimediaMoving(appFormReference = this.s1Application?.appFormReference) {
    this.apiService
      .findByParam(
        ENDPOINTS.applicationMultimedia,
        `app-form-reference/${appFormReference}`
      )
      .subscribe(
        (res: any) => {
          if (res.responseData.data.length > 0) {
            const multimediaList = new Set(
              res.responseData.data.map(
                (mappedList) =>
                  `${mappedList?.typeOfMedium?.description} (${mappedList?.multimediaSizeLength})`
              )
            );

            this.typeOfMediumDisplay = "";
            let mediaCtrl = 1;
            for (const media of multimediaList) {
              if (mediaCtrl !== multimediaList.size) {
                this.typeOfMediumDisplay += `${media}, `;
                mediaCtrl++;
              } else {
                this.typeOfMediumDisplay += media;
              }
            }

            this.multipleS1ApplicationList.forEach((element, index) => {
              if (element.appFormReference === appFormReference) {
                this.multipleS1ApplicationList[index] = {
                  ...this.multipleS1ApplicationList[index],
                  typeOfMediumDisplay: this.typeOfMediumDisplay,
                };
              }
            });
            this.dataSource = new MatTableDataSource<any>(
              this.multipleS1ApplicationList
            );
            this.dataSource.paginator = this.paginator;

            this.isDisabledSubmitButton = this.multipleS1ApplicationList.filter(
              (filteredList) => !filteredList.typeOfMediumDisplay
            ).length
              ? true
              : false;
          } else {
            this.typeOfMediumDisplay = "";
          }
        },
        (err: any) => {
          this.typeOfMediumDisplay = "";
        }
      );
  }
}
