import { USER_ROLES } from './../../../../../../shared/utils/constants';
import { Component, Inject, Input, OnDestroy, OnInit, Optional } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { DomSanitizer } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { AttachmentDocument, Payment, S1Application } from '@models';
import { ApiService, DataStorageService, SweetAlertService } from '@services';
import { ATTACHMENT_TYPE, ENDPOINTS, PAYMENT_STATUS } from '@shared';
import { ApplicationSingleMedia } from 'app/core/models/application-single-media.model';
import { FileStorageService } from 'app/core/services/file.storage.service';
import { NgxSpinnerService } from 'ngx-spinner';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-modal-single-application',
  templateUrl: './modal-single-application.component.html',
  styleUrls: ['./modal-single-application.component.scss']
})
export class ModalSingleApplicationComponent implements OnInit, OnDestroy {

  @Input() s1Application = new S1Application();
  attachmentDocument = new AttachmentDocument();
  applicationSingleMedia = new ApplicationSingleMedia();
  payment = new Payment();

  paymentTypeList = [];
  attachmentDocuments = [];


  paymentType: any;
  safeURLProofOfPayment: any;
  selectedFiles: any;
  currentUser: any;
  attachment: any;
  previewDocument = false;
  isSubmitted = false;
  paymentDetails: any;
  typeOfMediumDisplay = '';

  paymentForm: FormGroup;

  constructor(
    private sanitizer: DomSanitizer,
    private apiService: ApiService,
    public dataStorage: DataStorageService,
    private fileStorageService: FileStorageService,
    private sweetAlertService: SweetAlertService,
    private router: Router,
    private spinner: NgxSpinnerService,
    @Optional() public dialogRef: MatDialogRef<ModalSingleApplicationComponent>,
    @Optional() @Inject(MAT_DIALOG_DATA) public data: any
  ) {
    if (data) {
      if (data) {
        this.s1Application = data;
        console.log('S1', this.s1Application);

        switch (this.s1Application?.refTypeOfMedium || this.payment?.applicationFormS2?.applicationForm?.refTypeOfMedium) {
          case 'SINGLE MEDIA':
            this.getApplicationSingleMedia();
            break;
          case 'MULTIMEDIA-MOVING':
            this.getMultimediaMoving();
            break;
          case 'MULTIMEDIA-STATIC':
            this.getMultimediaStatic();
            break;
          default:
            break;
        }
        this.getPaymentDetails();
      }
    }
  }

  ngOnInit(): void {
    this.currentUser = this.dataStorage.getUserAccount();
    this.getPaymentList();
  }

  getPaymentDetails() {
    this.apiService.findAll(ENDPOINTS.paymentDetails + this.s1Application.id).subscribe((res: any) => {
      this.paymentDetails = res;
      this.paymentDetails.vat = this.paymentDetails.vat.toFixed(2);
    });
  }

  getPaymentList() {
    this.apiService.findAll(ENDPOINTS.paymentType).subscribe((res: any) => {
      this.paymentTypeList = res.responseData.data;
      if (this.currentUser.userRole.name === USER_ROLES.ROLE_APPLICANT) {
        const creditCardIndex = this.paymentTypeList
          .findIndex((element) => element.paymentDescription === 'Credit Card');
        this.paymentTypeList.splice(creditCardIndex, 1);
      }
    });
  }

  selectFile(event) {
    const attachmentSize = event.target.files[0].size;

    if (event.target.files[0].type === 'application/pdf') {
      if (attachmentSize > 15000000) { // 1MB = 1M
        this.sweetAlertService.customErrorMessage(`Attachments' total file size should not exceed 15MB.`);
        event.target.value = '';
        return;
      } else {
        const pdfURL = URL.createObjectURL(event.target.files[0]);
        this.safeURLProofOfPayment = this.sanitizer.bypassSecurityTrustResourceUrl(pdfURL);
        this.previewDocument = true;
        // env.showSpinner = true;
        const reader = new FileReader();
        const [file] = event.target.files;

        reader.readAsDataURL(file);
        reader.onload = () => {
        };
      }

      this.selectedFiles = event.target.files;
    } else {
      this.sweetAlertService.customErrorMessage('Attachment should be in PDF format only.');
      event.target.value = '';
      return;
    }
  }

  uploadDocument(s1Application) {
    Swal.fire({
      text: 'Are you sure you want to submit this PROOF OF PAYMENT?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.spinner.show();
        let params = '';
        params = '?&attachmentType=' + ATTACHMENT_TYPE.PAYMENT + '&submissionType=ORIGINAL';

        this.fileStorageService.pushFileToStorage(ENDPOINTS.attachmentDocument, this.selectedFiles.item(0), params)
          .subscribe(res => {
            const obj = res.responseData.data;
            console.log(obj);
            this.s1Application.paymentAttachmentReference = obj.paymentAttachmentReference;
            this.payment.paymentAttachmentReference = obj.paymentAttachmentReference;
            this.payment.amountDue = s1Application.amount;
            this.payment.applicationForm = s1Application;
            this.payment.appFormReference = s1Application.appFormReference;
            this.payment.filename = obj.filename;
            this.saveProofOfPayment();
          }, (err: any) => {
            this.spinner.hide();
            this.sweetAlertService.error(err);
          }, () => {
            // actions after subscription
            this.spinner.hide();
          });
      }
    });
  }

  saveProofOfPayment() {
    this.spinner.show();
    console.log('Saving payment...');
    this.payment.id = 0;
    this.payment.officialReceiptNo = '';
    this.payment.paymentMethod = null;
    console.log('PAYMENT : ', this.payment);
    this.apiService.save(`${ENDPOINTS.applicationFormPresentor}payment-type-id/${this.paymentType.id}/payment/submit`, this.payment)
      .subscribe((res: any) => {
        console.log('SAVED STATUS: ', res);
        if (res.responseData.data) {
          this.safeURLProofOfPayment = this.sanitizer.bypassSecurityTrustResourceUrl(res.responseData.data.fileURL);
          this.isSubmitted = true;
          this.sweetAlertService.customSuccessMessage('Proof of payment successfully submitted.');
          this.closeModal();
        } else {
          this.sweetAlertService.customErrorMessage('Error saving payment.');
        }

        this.spinner.hide();
        // this.previewDocument = true;
        this.selectedFiles = undefined;
        this.router.navigateByUrl('asc/page/application/single-application');
      }, (err) => {
        console.log(err);
        this.sweetAlertService.error(err);
        this.spinner.hide();
      }, () => {
        this.spinner.hide();
      });
  }

  closeModal(): void {
    this.dialogRef.close();
    this.router.navigate(['asc/page/application/single-application']);
  }
  ngOnDestroy(): void {
    // this.s1Application = null;
  }

  getApplicationSingleMedia() {
    this.apiService.findByParam(ENDPOINTS.applicationSingleMedia,
      `app-form-reference/${this.s1Application?.appFormReference}`).subscribe(
        (res: any) => {
          if (res.responseData.data.length > 0) {
            this.applicationSingleMedia = res.responseData.data[0];
            this.typeOfMediumDisplay = `${this.applicationSingleMedia?.mediumExecution?.typeOfMedium?.description} (${this.applicationSingleMedia?.sizeLength})`;
          } else {
            this.typeOfMediumDisplay = '';
          }
        }, (err: any) => {
          this.typeOfMediumDisplay = '';
        });
  }

  getMultimediaStatic() {
    this.apiService.findByParam(ENDPOINTS.applicationMultimedia,
      `app-form-reference/${this.s1Application?.appFormReference}`).subscribe(
        (res: any) => {
          if (res.responseData.data.length > 0) {
            const multimediaList = new Set(res.responseData.data.map(mappedList => `${mappedList?.typeOfMedium?.description} (${mappedList?.multimediaSizeLength})`));

            this.typeOfMediumDisplay = '';
            let mediaCtrl = 1;
            for (const media of multimediaList) {
              if (mediaCtrl !== multimediaList.size) {
                this.typeOfMediumDisplay += `${media}, `;
                mediaCtrl++;
              } else {
                this.typeOfMediumDisplay += media;
              }
            }
          } else {
            this.typeOfMediumDisplay = '';
          }
        }, (err: any) => {
          this.typeOfMediumDisplay = '';
        });
  }

  getMultimediaMoving() {
    this.apiService.findByParam(ENDPOINTS.applicationMultimedia,
      `app-form-reference/${this.s1Application?.appFormReference}`).subscribe(
        (res: any) => {
          if (res.responseData.data.length > 0) {
            const multimediaList = new Set(res.responseData.data.map(mappedList => `${mappedList?.typeOfMedium?.description} (${mappedList?.multimediaSizeLength})`));

            this.typeOfMediumDisplay = '';
            let mediaCtrl = 1;
            for (const media of multimediaList) {
              if (mediaCtrl !== multimediaList.size) {
                this.typeOfMediumDisplay += `${media}, `;
                mediaCtrl++;
              } else {
                this.typeOfMediumDisplay += media;
              }
            }
          } else {
            this.typeOfMediumDisplay = '';
          }
        }, (err: any) => {
          this.typeOfMediumDisplay = '';
        });
  }
}
