import { S2Application, UserAccount } from '@models';
export class CommentThread {
    id:                  number;
    appFormReference:    string;
    applicationFormType: string;
    comment:             string;
    actionBy:            UserAccount;
    commentType:         string;
    role:                string;
    actionDate:          Date;
    isReply:             boolean;
    replies:             Array<CommentThread>;
    parentId:            number;
    parent:              any;
    replyEditorIsEnabled: boolean;
    applicationFormS2: S2Application;
    constructor(){
        this.parent = {};
        this.replyEditorIsEnabled = false;
        this.isReply = false;
    }
}