import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { FormListComponent } from '@fragments';
import { S1Application, S2Application } from '@models';
import { ApiService, SweetAlertService } from '@services';
import { ENDPOINTS, USER_ROLES } from '@shared';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-reassignment',
  templateUrl: './reassignment.component.html',
  styleUrls: ['./reassignment.component.scss']
})
export class ReassignmentComponent implements OnInit {
  @ViewChild(FormListComponent) formListComponent: FormListComponent;

  // ojects
  s1Application = new S1Application();
  s2Application = new S2Application();

  // lists
  userList = [];

  // for displayed list
  // s1
  s1ApplicationList = [];
  s1ApplicationDataSource: MatTableDataSource<S1Application>;
  s1ApplicationDisplayedColumns = [
    'referenceCode',
    'adTitle',
    'applicationOwner',
    'assignedEncoder',
    'assignedAdSpecialist',
    'assignedScreener',
    'assignedReviewer',
    'assignedUser',
  ];

  // s2
  // TODO update when endpoint is available
  s2ApplicationList = [];
  s2ApplicationDataSource: MatTableDataSource<S2Application>;
  s2ApplicationDisplayedColumns = [
    'referenceCode',
    'adTitle',
    'applicationOwner',
    'assignedEncoder',
    'assignedAdSpecialist',
    // 'assignedScreener',
    // 'assignedReviewer',
    'assignedUser',
  ];

  // form
  s1ApplicationForm: FormGroup;

  // others
  applicationOwnerName = '';
  displayedContent = 'S1';

  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService,
    private spinner: NgxSpinnerService,
  ) { }

  ngOnInit() {
    this.getS1ApplicationList();
    this.setFormValidator();
    this.getUserList();
  }

  getS1ApplicationList(applicationType = 'S1'): void {
    this.spinner.show();
    if (applicationType === 'S1') {
      this.apiService.findAll(ENDPOINTS.admin).subscribe((res: any) => {
        this.s1ApplicationList = res.responseData.data;
        this.s1ApplicationList = this.s1ApplicationList.filter(filteredS1List => !filteredS1List.appliedS2);

        this.s1ApplicationDataSource = new MatTableDataSource<S1Application>(this.s1ApplicationList);
        this.s1ApplicationDataSource.paginator = this.formListComponent.paginator;
        this.s1ApplicationDataSource.sort = this.formListComponent.sort;
      }, (err) => {
        console.error(err);
        this.spinner.hide();
      }, () => {
        this.spinner.hide();
      });
    } else if (applicationType === 'S2') {
      this.apiService.findAll(ENDPOINTS.s2Application).subscribe((res: any) => {
        this.s2ApplicationList = res.responseData.data;

        this.s2ApplicationDataSource = new MatTableDataSource<S2Application>(this.s2ApplicationList);
        this.s2ApplicationDataSource.paginator = this.formListComponent.paginator;
        this.s2ApplicationDataSource.sort = this.formListComponent.sort;
      }, (err) => {
        console.error(err);
        this.spinner.hide();
      }, () => {
        this.spinner.hide();
      });
    }
  }

  getS1Application(): void {
    this.apiService.findById(ENDPOINTS.applicationForm, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {
        this.s1Application = res.responseData.data;
        this.applicationOwnerName = this.s1Application.applicationOwner
          ? this.s1Application.applicationOwner.lastName + ', ' + this.s1Application.applicationOwner.firstName
          : '';
        if (this.s1Application.assignedUser) {
          this.getUserList(this.s1Application.assignedUser.userRole.name);
        } else {
          this.getUserList('N/A');
        }

      }
    });
  }

  getS2Application(): void {
    this.apiService.findById(ENDPOINTS.applicationFormS2, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {

        this.s2Application = res.responseData.data;
        if (this.s2Application.applicationForm) {
          this.applicationOwnerName = this.s2Application.applicationForm.applicationOwner
            ? this.s2Application.applicationForm.applicationOwner.lastName + ', '
            + this.s2Application.applicationForm.applicationOwner.firstName
            : '';
        }
        if (this.s2Application.assignedUser) {
          this.getUserList(this.s2Application.assignedUser.userRole.name);
        } else {
          this.getUserList('N/A');
        }

      }
    });
  }

  update(): void {
    this.spinner.show();

    let endpoint = '';
    let currentObj: any;
    if (this.displayedContent === 'S1') {
      endpoint = ENDPOINTS.applicationReassignment
      currentObj = this.s1Application;
    } else if (this.displayedContent === 'S2') {
      endpoint = ENDPOINTS.s2ApplicationReassignment;
      // endpoint = ENDPOINTS.applicationFormS2;
      if (this.s2Application.actionStatus === 'ASSIGN_TO_ENCODER') {
        this.s2Application.assignedEncoder = this.s2Application.assignedUser;
      } else if (this.s2Application.actionStatus === 'ASSIGN_TO_ADSPE') {
        this.s2Application.assignedAdSpecialist = this.s2Application.assignedUser;
      }

      currentObj = this.s2Application;
    }

    this.apiService.save(endpoint, currentObj).subscribe(
      (res: any) => {
        if (res) {
          this.spinner.hide();
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.spinner.hide();
        this.sweetAlertService.error(err);
      }, () => {
        // actions after subscription
        this.spinner.hide();
        this.getUserList();
        this.resetFormValidator();
        if (this.displayedContent === 'S1') {
          this.getS1ApplicationList();
        } else if (this.displayedContent === 'S2') {
          this.getS1ApplicationList('S2');
        }
      });
  }

  setFormValidator(): void {
    this.s1ApplicationForm = this.formBuilder.group({
      referenceCode: [''],
      adTitle: [''],
      applicationOwner: [''],
      assignedAdSpecialist: [''],
      assignedEncoder: [''],
      assignedReviewer: [''],
      assignedScreener: [''],
      assignedUser: [''],
    });
  }

  resetFormValidator(): void {
    this.s1Application = new S1Application();
    this.s2Application = new S2Application();
    this.s1ApplicationForm.markAsUntouched();
    this.s1ApplicationForm.markAsPristine();
  }


  getUserList(userRole = '') {
    this.apiService.findAll(ENDPOINTS.userAccount).subscribe(
      (res: any) => {
        this.userList = res.responseData.data;
        this.userList = this.userList.filter(
          filteredUsers => filteredUsers?.userRole?.name !== USER_ROLES.ROLE_APPLICANT);
        if (userRole) {
          this.userList = this.userList.filter(
            filteredUsers => filteredUsers?.userRole?.name === userRole);
        }
      }
    );
  }

  compareAssignedUser(value: any, selected: any): boolean {
    if (value && selected) {
      return value.id === selected.id;
    }
  }

  changeTab(evt) {
    if (evt.tab) {
      this.displayedContent = evt.tab.textLabel;
    }

    if (evt.index === 0) {
      this.getS1ApplicationList();
    } else if (evt.index === 1) {
      this.getS1ApplicationList('S2');
    }
  }

}
