import { Component, Inject, OnInit, Optional, ViewChild } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTable, MatTableDataSource } from '@angular/material/table';
import { PerformanceMetrics } from '@models';
import { ApiService } from '@services';
import { ENDPOINTS } from '@shared';

@Component({
  selector: 'app-performance-metrics',
  templateUrl: './performance-metrics.component.html',
  styleUrls: ['./performance-metrics.component.scss']
})
export class PerformanceMetricsComponent implements OnInit {
  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: false }) sort: MatSort;
  @ViewChild(MatTable, { static: false }) table: MatTable<PerformanceMetrics>;

  dataSource: MatTableDataSource<PerformanceMetrics>;
  displayedColumns: string[] = [
    // 'referenceCode',
    // 'appFormReference',
    'movement',
    'lastUpdate',
    'duration',
    'actionBy'
  ];

  performanceMetricsList = [];
  referenceCode: any;

  constructor(
    private apiService: ApiService,
    @Optional() public dialogRef: MatDialogRef<PerformanceMetricsComponent>,
    @Optional() @Inject(MAT_DIALOG_DATA) public data: any
  ) {
    if (data) {
      this.getPerformanceMatricsList(data.appFormReference);
      this.referenceCode = data.referenceCode;
    }
  }

  ngOnInit(): void {
  }

  getPerformanceMatricsList(appFormReference) {
    this.apiService.findByParam(ENDPOINTS.performanceMetrics, appFormReference).subscribe((res: any) => {
      this.performanceMetricsList = res;
      this.dataSource = new MatTableDataSource<PerformanceMetrics>(this.performanceMetricsList);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    });
  }

  closeDialog() {
    this.dialogRef.close();
  }

}
