import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTable, MatTableDataSource } from '@angular/material/table';
import { ActivatedRoute, Router } from '@angular/router';
import { S2Application } from '@models';
import { ApiService, DataStorageService, SweetAlertService } from '@services';
import { APPLICATION_STATUS, ENDPOINTS, FORM_STATUS, ROLES, SUBMISSION_TYPE, USER_ROLES } from '@shared';
import { ApplicationRequest } from 'app/core/models/application-request.model';
import { ApplicationCountS2 } from 'app/core/models/applicationCountS2.model';
import { MultipleApplication } from 'app/core/models/multiple-application.model';
import { CommonServices } from 'app/core/services/common-services.service';
import { RequestForSpecialClearingComponent } from 'app/modules/fragments/request-for-special-clearing/request-for-special-clearing.component';
import { NgxSpinnerService } from 'ngx-spinner';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-list-s2-application',
  templateUrl: './list-s2-application.component.html',
  styleUrls: ['./list-s2-application.component.scss']
})
export class ListS2ApplicationComponent implements OnInit {
  @ViewChild(MatPaginator, { static: false }) paginatorFalse: MatPaginator;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  pageSize = 10;
  pageNo = 0;
  pageSizeOptions: number[] = [10, 20, 50];
  @ViewChild(MatSort, { static: false }) sort: MatSort;
  @ViewChild(MatTable, { static: false }) table: MatTable<S2Application>;
  dataSource: MatTableDataSource<S2Application>; // in html but not existing in component -- encountered during 'ng serve --aot'
  displayedColumns = ['referenceCode', 'typeOfApplication', 'companyName', 'brand', 'typeOfMedium', 'status', 'internalStatus', 'icon'];
  s2ApplicationList = [];
  s2TotalItems = 0;
  isAscUser: boolean = false

  // For clearance list
  forClearanceDataSource: MatTableDataSource<S2Application>;
  s2ApplicationListForClearance = [];
  forClearanceDisplayedColumns = ['referenceCode', 'typeOfApplication', 'companyName', 'brand', 'typeOfMedium', 'status', 'icon'];
  s2SpecialTotalItems = 0;

  s2SpecialApplicationDataSource: MatTableDataSource<MultipleApplication>;
  s2SpecialApplicationList = []
  s2SpecialApplicationDisplayedColumns = ['adTitle', 'typeOfApplication', 'companyName', 'brand', 'status'];

  applicationRequest = new ApplicationRequest();

  currentUser: any;
  viewApplicationType: string;
  applicationTypeTitle: string;
  currentPage = 1;
  // currentSize = 100;
  originalTabDataCount = 0;
  complianceTabDataCount = 0;
  releasedAppealTabDataCount = 0;
  toaTabDataCount = 0;
  click = 0;
  timeLeft: number = 60;
  interval: any;
  refreshButtonEnabled = true;
  timerDisplay: any;
  isTimerValueDisplay = false;
  s2ApplicationCountList = [];

  displayedContent = 'ORIGINAL';

  s2Application = new S2Application();
  applicationCountS2 = new ApplicationCountS2();

  isSuperAdmin = false;
  isAdmin = false;
  isAccounting = false;
  isApplicant = false;
  isScreener = false;
  isReviewer = false;
  isAdSpecialist = false;
  isEvaluator = false;
  ascUserRoleList = [...ROLES.filter((role: any) => role.ascUser === true).map((role: any) => role.role)];
  constructor(
    private apiService: ApiService,
    private dataStorage: DataStorageService,
    private router: Router,
    private sweetAlertService: SweetAlertService,
    private dialog: MatDialog,
    private spinner: NgxSpinnerService,
    private route: ActivatedRoute,
    private commonService: CommonServices
  ) { }

  ngOnInit(): void {
    this.isAscUser = this.ascUserRoleList.includes(this.dataStorage.getUserRole());
    this.currentUser = this.dataStorage.getUserAccount();
    this.viewApplicationType = this.route.parent.snapshot.url[0].path;
    this.getListCount();
    if (this.currentUser.userRole.name === USER_ROLES.ROLE_APPLICANT) {
      this.displayedColumns = ['referenceCode', 'typeOfApplication', 'companyName', 'brand', 'typeOfMedium', 'status', 'icon'];
    }

    if (this.viewApplicationType === 'special') {
      this.applicationTypeTitle = 'SPECIAL CLEARING';
      this.getSpecialS2ApplicationList();
    } else {
      this.applicationTypeTitle = 'INDIVIDUAL';
      this.getList();
    }

    this.initUserRole();
  }


  initUserRole() {
    if (this.currentUser.userRole.name === USER_ROLES.ROLE_SUPER_ADMIN) {
      this.isSuperAdmin = true;
    } else if (this.currentUser.userRole.name === USER_ROLES.ROLE_ADMIN) {
      this.isAdmin = true;
    } else if (this.currentUser.userRole.name === USER_ROLES.ROLE_ACCOUNTING) {
      this.isAccounting = true;
    } else if (this.currentUser.userRole.name === USER_ROLES.ROLE_APPLICANT) {
      this.isApplicant = true;
    } else if (this.currentUser.userRole.name === USER_ROLES.ROLE_SCREENER) {
      this.isScreener = true;
    } else if (this.currentUser.userRole.name === USER_ROLES.ROLE_REVIEWER) {
      this.isReviewer = true;
    } else if (this.currentUser.userRole.name === USER_ROLES.ROLE_AD_SPECIALIST) {
      this.isAdSpecialist = true;
    } else if (this.currentUser.userRole.name === USER_ROLES.ROLE_ENCODER) {
      this.isEvaluator = true;
    }
  }


  getListCount(): void {
    let endpoint;
    if (this.currentUser.userRole.name === USER_ROLES.ROLE_APPLICANT) {
      endpoint = ENDPOINTS.badgeCountByApplicationOwnerS2;
    } else {
      endpoint = ENDPOINTS.badgeCountByAscUserS2;
    }
    this.apiService.findAll(endpoint + this.currentUser.id).subscribe(
      (res: any) => {
        this.applicationCountS2 = res;
      })
  }

  getList(submissionType = 'ORIGINAL'): void {

    this.currentPage = this.pageNo;
    this.currentPage++;
    if (this.currentPage === 0) this.currentPage++;
    let param = submissionType.toUpperCase();

    if (param === SUBMISSION_TYPE.COMPLIANCE && this.currentUser.userRole.name === USER_ROLES.ROLE_APPLICANT) {
      param = `&complianceForm=true`;
    } else if (param === SUBMISSION_TYPE.COMPLIANCE && this.currentUser.userRole.name !== USER_ROLES.ROLE_APPLICANT) {
      param = `&complianceForm=true`;
    } else if (param === SUBMISSION_TYPE.TOA) {
      param = `&hasToa=true`;
    } else {
      param = `&submissionType=${param}&complianceForm=false`;
    }

    this.spinner.show();
    let endpoint = `custom/search?page=${this.currentPage}&size=${this.pageSize}${param}`;

    if (this.currentUser.userRole.name === USER_ROLES.ROLE_APPLICANT) {
      endpoint = ENDPOINTS.s2ApplicationFormPresentor + endpoint + `&applicationOwnerId=${this.currentUser.id}`;
    } else if (this.currentUser.userRole.name === USER_ROLES.ROLE_ENCODER) {
      endpoint = ENDPOINTS.s2ApplicationFormEncoder + endpoint + `&assignedEncoderId=${this.currentUser.id}`;
    } else if (this.currentUser.userRole.name === USER_ROLES.ROLE_AD_SPECIALIST) {
      endpoint = ENDPOINTS.s2ApplicationFormAdSpe + endpoint + `&assignedAdSpecialistId=${this.currentUser.id}`;
    } else if (this.currentUser.userRole.name === USER_ROLES.ROLE_ADMIN || this.currentUser.userRole.name === USER_ROLES.ROLE_SUPER_ADMIN) {
      endpoint = ENDPOINTS.s2ApplicationFormAdmin + endpoint;
    } else {
      endpoint = ENDPOINTS.s2Application + endpoint + `&applicationOwnerId=${this.currentUser.id}`;
    }

    // console.log(endpoint);
    this.apiService.findAll(endpoint).subscribe(
      (res: any) => {
        this.s2ApplicationList = res.data;

        this.s2TotalItems = res.totalItems;
        console.log('s2ApplicationList: ', res);
        this.dataSource = new MatTableDataSource<S2Application>(this.s2ApplicationList);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
        this.hideSpinner();
      }, (err: any) => {
        this.hideSpinner();
      }
    )
  }

  hideSpinner() {
    setTimeout(() => {
      this.spinner.hide();
    }, 1);
  }

  getSpecialS2ApplicationList(): void {
    let endpoint = ''; let obj;
    if (this.currentUser.userRole.name === USER_ROLES.ROLE_ADMIN || this.currentUser.userRole.name === USER_ROLES.ROLE_SUPER_ADMIN) {
      obj = {
        statuses: ['FOR_APPROVAL', 'APPROVED', 'DISAPPROVED']
      }
      endpoint = ENDPOINTS.s2SpecialClearingApplicationRequestsAdmin;
    } else {
      obj = {
        applicationOwnerId: this.currentUser.id,
        statuses: ['FOR_APPROVAL', 'APPROVED', 'DISAPPROVED']
      }
      endpoint = ENDPOINTS.s2SpecialClearingApplicationRequests;
    }

    this.currentPage = this.pageNo;
    this.currentPage++;
    if (this.currentPage === 0) this.currentPage++;

    this.apiService.save(`${endpoint}?page=${this.currentPage}&size=${this.pageSize}`, obj)
      .subscribe((res: any) => {
        this.s2SpecialApplicationList = res.data;

        console.log('s2SpecialApplicationList: ', res);
        this.s2SpecialTotalItems = res.totalItems;
        this.s2SpecialApplicationDataSource = new MatTableDataSource<MultipleApplication>(this.s2SpecialApplicationList);
        this.s2SpecialApplicationDataSource.paginator = this.paginator;
        this.s2SpecialApplicationDataSource.sort = this.sort;
        setTimeout(() => {
          this.spinner.hide();
        }, 1);
      }, (err: any) => {
        this.spinner.hide();
      });
  }


  pageEvents(event: any) {
    this.pageSize = event.pageSize;

    if (event.pageIndex > this.pageNo) {
      this.pageNo++;
    } else {
      this.pageNo--;
    }

    if (event.pageSize > event.length) {
      this.pageNo = 0;
    }

    this.getList();
  }

  setPageSizeOptions(setPageSizeOptionsInput: string) {
    if (setPageSizeOptionsInput) {
      this.pageSizeOptions = setPageSizeOptionsInput.split(',').map(str => +str);
    }
  }

  viewApplication(data: any): void {
    this.s2Application = data;
    console.log('S2 FORM: ', this.s2Application);

    if (data.formStatus === FORM_STATUS.OPEN && this.s2Application.internalStatus !== APPLICATION_STATUS.S1_APPLICATION_REJECTED
      && (this.currentUser.userRole.name !== USER_ROLES.ROLE_APPLICANT && this.currentUser.userRole.name !== USER_ROLES.ROLE_ADMIN
        && this.currentUser.userRole.name !== USER_ROLES.ROLE_SUPER_ADMIN
        && this.currentUser.userRole.name !== USER_ROLES.ROLE_AD_SPECIALIST) // s2form will be assigned to s1form adspe
    ) {
      Swal.fire({
        text: 'Are you sure you want to open this application? This application will be LOCKED on your account.',
        icon: 'question',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Confirm'
      }).then((result) => {
        if (result.value) {
          this.spinner.show();
          this.s2Application.assignedEncoder = this.currentUser;
          // wait for BE
          this.apiService.save(ENDPOINTS.s2ApplicationFormEncoder + ENDPOINTS.takeApplication, this.s2Application).subscribe(
            (res: any) => {
              this.spinner.hide();
              this.sweetAlertService.customSuccessMessage('Application successfully assigned to you.', 3);
              this.router.navigate(['asc/page/application/s2/update/', data.id]);
            }, (err: any) => {
              this.spinner.hide();
            });
        }
      });
    } else {
      this.router.navigate(['asc/page/application/s2/update/', data.id]);
    }

  }

  applyApplicationForToa(data: any): void {
    Swal.fire({
      text: 'Are you sure you want to apply this application to TOA?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.spinner.show();
        this.apiService.save(ENDPOINTS.s2ApplicationForm + 'clearance/presentor/submit', data).subscribe(
          (res: any) => {
            this.spinner.hide();
            this.sweetAlertService.customSuccessMessage('Application is applied for TOA successfully');
            this.router.navigateByUrl('asc/page/application/s2/lists');
          }, (err: any) => {
            this.spinner.hide();
          });
      }
    })
  }

  viewSpecialApplication(data: any): void {
    this.applicationRequest = data;
    this.s2Application = new S2Application();
    this.s2Application.specialClearing = true;
    this.s2Application.applicationForm = data;

    let dialogRef;
    dialogRef = this.dialog.open(RequestForSpecialClearingComponent, {
      width: '80%',
      height: '85%',
      data: {
        applicationRequest: this.applicationRequest,
        applicationType: 'Request for Special Clearing'
      }
    });
  }

  doFilterS1MultipleApplicationList(event) { } // in html but not existing in component -- encountered during 'ng serve --aot'

  getCompany(elementId) { } // in html but not existing in component -- encountered during 'ng serve --aot'

  doFilter(event) { } // in html but not existing in component -- encountered during 'ng serve --aot'

  changeTab(evt) {
    if (evt.tab) {
      this.displayedContent = evt.tab.textLabel;
      if (this.displayedContent === 'For Appeal') {
        this.getForDecisionAndComment();
      } else {
        this.getList(this.displayedContent);
      }
    }
  }


  getForDecisionAndComment() {
    let endpoint = '';

    // APPEAL
    if (this.isAdSpecialist) {
      endpoint = ENDPOINTS.s2ApplicationFormAdSpe + `custom/search?assignedAdSpecialistId=${this.currentUser.id}&hasOngoingAppeal=true&page=${this.currentPage}&size=${this.pageSize}&submissionType=ORIGINAL`;
    }
    else if (this.isAdmin || this.isSuperAdmin) {
      endpoint = ENDPOINTS.s2ApplicationFormAdmin + `custom/search?hasOngoingAppeal=true&page=${this.currentPage}&size=${this.pageSize}`;
    }

    this.apiService.findAll(endpoint)
      .subscribe((res: any) => {
        if (res) {
          this.s2ApplicationList = res?.data;
          console.log('Appeal: ', this.s2ApplicationList);

          this.s2TotalItems = res.totalItems;
          this.dataSource = new MatTableDataSource<S2Application>(this.s2ApplicationList);
          this.dataSource.paginator = this.paginatorFalse;
          this.dataSource.sort = this.sort;
          setTimeout(() => {
            this.spinner.hide();
          }, 1);
        } else {
          this.s2ApplicationList = [];
        }
      }, (err) => {
        setTimeout(() => {
          this.spinner.hide();
        }, 1);
      });
  }

  sortEvent() {
    this.click++;
    if (this.click % 2 === 0) {
      this.dataSource = new MatTableDataSource<S2Application>(this.s2ApplicationList);
      this.dataSource.paginator = this.paginator;
      return this.dataSource.data.sort((a, b) => a.id - b.id);
    } else {
      this.dataSource = new MatTableDataSource<S2Application>(this.s2ApplicationList);
      this.dataSource.paginator = this.paginator;
      return this.dataSource.data.reverse();
    }
    // for random sort
    // this.dataSource.data.sort(() => Math.random() - 0.5)
  }

  refresh() {
    this.commonService.sendClickEvent();
    this.startTimer();
  }

  startTimer() {
    this.interval = setInterval(() => {
      this.timeLeft--;
      if(this.timeLeft > 0){
        this.refreshButtonEnabled = false;
        this.isTimerValueDisplay = true;
      }else{
        this.refreshButtonEnabled = true;
        this.isTimerValueDisplay = false;
        this.timeLeft = 60;
        clearInterval(this.interval)
      }
      this.timerDisplay=this.transform(this.timeLeft)
    },1000)
  }

  transform(value: number): string {
    const minutes: number = Math.floor(value / 60);
    return minutes + ':' + (value - minutes * 60);
  }

}
